/**
 * Config api domain
 * add .env
 * APP_DOMAIN=yourdomain.com
 * APP_PROTOCOL=http
 * MIX_APP_PROTOCOL="${APP_PROTOCOL}"
 * MIX_APP_DOMAIN="${APP_DOMAIN}"
 */
const APP_PROTOCOL = process.env.MIX_APP_PROTOCOL;

const _baseDevURL = 'http://localhost:8000';
const _baseProURL = APP_PROTOCOL + '://' + process.env.MIX_APP_DOMAIN;

const APP_DOMAIN = process.env.NODE_ENV === 'production' ? _baseProURL : _baseDevURL;

export default {
    API_URL: APP_DOMAIN + '/api/',
    BASE_URL: APP_DOMAIN,
};
