<script>
import { post, put } from "../../../api/index.js";
import Vue from "vue";
import { getWithContentWrap } from "../../../helpers/helpers";

export default {
  name: "PackageItem",
  data() {
    return {
      emails:[
        { kode: true, text: 'Yes'},
        { kode: false, text: 'No'}
      ],
      drawerRight: false,
      check: "",
      rules: {
        required: value => !!value || "Required.",
        decimal: value => {
          const pattern = /^[\d]+[\.][\d]{2}$/;
          return pattern.test(value) || "E.g:1.00";
        },
        number: value => {
          const pattern = /^[0-9]\d*$/;
          return pattern.test(value) || "Pls input number.";
        }
      },
      item: {
        sms: false,
        email_service: false
      },
      alertStt: false,
      alertType: "success",
      alertMes: "",
      key: 0,
      data: {},
      width: 0,
      drawerHeaderStt: null
    };
  },
  computed: {
    widthComputed() {
      return this.width;
    }
  },
  methods: {
    getCurrentWithContentWrap() {
      return getWithContentWrap(this.drawerHeaderStt);
    },
    resetData() {
      this.$refs.form.reset();
    },
    stopdrawerRight() {
      this.drawerRight = false;
    },
    savePackageAdd() {
      if (this.$refs.form.validate()) {
        let url = "packages";
        post(url, this.item)
          .then(res => {
            if (res && res.success) {
              setTimeout(function() {
                Vue.notify({
                  group: "loggedIn",
                  type: "success",
                  text: "Add Package Success!"
                });
              }, 500);
              this.drawerRight = false;
              this.$root.$emit("reload-table", true);
              this.item = {
                sms: false,
                email_service: false
              };
            }
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    unDisableItem(key) {
      this.key = key;
    },
    editItem(field_name, value) {
      var field = {
        field_name: field_name,
        value: value
      };

      this.fetchData(field);
    },

    fetchData(field) {
      post("edit/package/" + this.data.id, field)
        .then(res => {
          if (res && res.success) {
            this.alertStt = true;
            this.alertType = "success";
            this.alertMes = "Update Successfully";
            setTimeout(() => {
              this.alertStt = false;
            }, 1500);
            this.key = 0;
            this.$root.$emit("editItemSucess");
          }
        })
        .catch(e => {
          this.alertStt = true;
          this.alertType = "error";
          this.alertMes = "System Error Occurred";
          setTimeout(() => {
            this.alertStt = false;
          }, 1500);
        });
    },

    editSetting(field_name, value) {
      var field = {
        field_name: field_name,
        value: value
      };

      this.fetchDataSetting(field);
    },

    fetchDataSetting(field) {
      post("edit/setting/" + this.data.setting_id, field)
        .then(res => {
          if (res && res.success) {
            this.alertStt = true;
            this.alertType = "success";
            this.alertMes = "Update Successfully";
            setTimeout(() => {
              this.alertStt = false;
            }, 1500);
            this.key = 0;
            this.$root.$emit("editItemSucess");
            // this.$root.$emit('closeDrawerItem', false)
          }
        })
        .catch(e => {
          this.alertStt = true;
          this.alertType = "error";
          this.alertMes = "System Error Occurred";
          setTimeout(() => {
            this.alertStt = false;
          }, 1500);
        });
    }
  },
  mounted() {
    this.$root.$on("change-status", res => {
      this.drawerRight = res.showDialog;
      this.check = res.check;
      this.resetData();
      this.width = this.getCurrentWithContentWrap();
    });

    this.$root.$on("data-packages", res => {
      this.data = res;
    });
  }
};
</script>

