<template>
    <v-container fluid>
        <app-card>
                <v-card-title flat color="white" class="py-2">
                    <span class="headline">Team</span>
                    <v-spacer/>
                    <input type="search" v-model="search" class="cari" id="search" name="search" placeholder="Search" @keyup="searchPhotographer()">
                    <v-btn
                            @click="showFromAdd"
                            class="btn-add"
                            color="#ededed"
                            icon
                            fab
                            small
                        >
                        <v-icon color="grey">fas fa-plus</v-icon>
                    </v-btn>
                </v-card-title>
                <!-- Table Component -->
                <v-data-table
                    :headers="headers"
                    :items="items"
                    class="elevation-5"
                    :pagination.sync="pagination"
                    :rows-per-page-items="rowsPerPageItems"
                    default-sort="id:desc"
                    :search="search"
                >
                    <v-progress-linear slot="progress" color="blue" indeterminate></v-progress-linear>
                    <!--Header -->
                    <template slot="headers" slot-scope="props">
                        <tr>
                            <th
                                v-for="header in props.headers"
                                :key="header.text"
                                :class="['column sortable', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
                                @click="changeSort(header.value)"
                            >
                                <div class="custom-header">
                                    <v-tooltip bottom>
                                        <span
                                            slot="activator"
                                            class="text-capitalize font-weight-bold"
                                        >{{ header.text }}</span>
                                        <span>{{ header.text }}</span>
                                    </v-tooltip>
                                    <v-icon v-if="header.value != 'actions'">arrow_upward</v-icon>
                                </div>
                            </th>
                        </tr>
                    </template>

                    <template slot="items" slot-scope="props">
                        <td>{{ props.item.id }}</td>
                        <td class="text-xs-left">{{ props.item.branch.name }}</td>
                        <td class="text-xs-left"><v-img :src="props.item.avatar"></v-img></td>
                        <td class="text-xs-left">{{ props.item.name }}</td>
                        <td class="text-xs-left">{{ props.item.name }}</td>
                        <td class="text-xs-left">{{ props.item.position }}</td>
                        <td class="text-xs-left">{{ props.item.phone_number }}</td>
                        <td class="text-xs-left">{{ props.item.status ? 'Active' : 'Inactive' }}</td>
                        <td class="text-xs-left d-flex">
                            <v-icon small class="mr-2 hover-icon" @click="showDetail(props.item)">visibility</v-icon>
                            <v-icon small class="mr-2 hover-icon" @click="showFormEdit(props.item)">edit</v-icon>
                            <v-icon small class="mr-2 hover-icon" @click="showDialog(props.item.id)">delete</v-icon>
                        </td>
                    </template>

                    <!--No data -->
                    <template slot="no-data">
                        <v-alert color="error" icon="warning">Sorry, nothing to display here :(</v-alert>
                    </template>

                    <!--Search no result -->
                    <v-alert
                        slot="no-results"
                        :value="true"
                        color="error"
                        icon="warning"
                    >Your search for "{{ search }}" found no results.</v-alert>
                </v-data-table>
                <!-- End Table Component -->
            </app-card>
        <photographer-add/>
        <photographer-edit/>
        <v-dialog v-model="dialog" persistent max-width="300">
            <v-card class="px-4">
                <v-card-text class="text-center px-0">
                    <p class="font-md p-2 font-weight-bold black--text">Delete?</p>
                    <span class="px-0">You will not be able to recover this Module!</span>
                </v-card-text>
                <v-card-actions>
                    <v-btn color="#dcdcdc" @click="dialog = false"><v-icon color="grey">close</v-icon>Cancel</v-btn>
                    <v-spacer />
                    <v-btn color="#dcdcdc" @click="deleteItem"><v-icon color="grey">delete</v-icon>Delete</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-container>
</template>
<script>
import { get, post, put, del, getWithData } from "../../../api/index.js";
import PhotographerDetail from "./PhotographerDetail";
import PhotographerAdd from "./PhotographerAdd";
import PhotographerEdit from "./PhotographerEdit";
import Lodash from "lodash";

export default {
  name: "Photographer",
  components: {
    PhotographerDetail,
    PhotographerAdd,
    PhotographerEdit
  },
  data() {
    return {
      headers: [
        { text: "ID", value: "id" },
        { text: "Branch", value: "branch.name", widht: '10%' },
        { text: "Image", value: "image" },
        { text: "Full Name", value: "full_name" },
        { text: "Nick Name", value: "nick_name" },
        { text: "Position", value: "position" },
        { text: "Phone", value: "phone_number" },
        { text: "Status", value: "status", sortable: false },
        { text: "Action", sortable: false }
      ],
      items: [],
      search: "",
      pagination: {
        rowsPerPage: 10
      },
      rowsPerPageItems: [
        10,
        25,
        50,
        100,
        { text: "$vuetify.dataIterator.rowsPerPageAll", value: -1 }
      ],
      company: JSON.parse(localStorage.getItem("user")),
      dialog: false,
      activeInfo: true,
      itemIdToDelete: "",
      loading: false,
      checkDetail: false,
      itemDetail: [],
      settings: {
        maxScrollbarLength: 160
      },
      photographerId: 0,
      activeItem: ""
    };
  },
  created() {
    this.fetchData(this.activeItem);
  },
  mounted() {
    this.$root.$on("reloadTablePhotographer", res => {
      this.fetchData(res.data.id);
    });

    this.$root.$on("reloadTablePhotographerAdd", res => {
      this.fetchData(this.activeItem);
    });
  },
  methods: {
    fetchData(activeItem) {
      get(
        "photographers?companyId=" + this.company.company_id
      ).then(response => {
        if (response && response.success) {
          this.items = response.data;
          if (activeItem == "") {
            this.photographerId = this.items[0].id;
          } else {
            this.photographerId = activeItem;
          }
          this.checkDetail = true;
          this.detailItem(this.photographerId);
        }
      });
    },

    deleteItem() {
      del(
          "photographer/" +
          this.itemIdToDelete +
          "?userId=" +
          this.company.id
      )
        .then(res => {
          if (res && res.success) {
            this.fetchData(this.activeItem);
            this.dialog = false;
            this.$notify({
              title: "Success",
              message: "Delete Item Successfully.",
              type: "success",
              duration: 2000
            });
            this.checkDetail = false;
          }
        })
        .catch(e => {
          console.log(e.response);
        });
    },

    detailItem(photographerId) {
      get("photographer/detail/" + photographerId)
        .then(res => {
          if (res && res.success) {
            this.itemDetail = res.data;
          }
        })
        .catch(e => {
          console.log(e.response);
        });
    },

    showInfo(item) {
      this.$root.$emit("showInfoPhototographer", {
        showNavigation: true,
        data: item
      });
    },

    showDialog(item) {
      this.dialog = true;
      this.itemIdToDelete = item;
    },

    showFromAdd() {
      this.$root.$emit("showPhotographerAdd", { showNavigation: true });
    },

    showFormEdit(item) {
      this.$root.$emit("showFormEditPhotgrapher", {
        showNavigation: true,
        data: item
      });
    },

    changeSort(column) {
      var columnsNoSearch = ["actions"];
      if (columnsNoSearch.indexOf(column) > -1) {
        return;
      }
      this.loading = true;
      if (this.pagination.sortBy === column) {
        this.pagination.descending = !this.pagination.descending;
      } else {
        this.pagination.sortBy = column;
        this.pagination.descending = false;
      }
      this.loading = false;
    },
    showDetail(item) {
      this.checkDetail = true;
      this.itemDetail = item;
      this.photographerId = item.id;
    },
    searchPhotographer: _.debounce(function() {
      get(
          "photographers?companyId=" +
          this.company.company_id +
          "&search=" +
          this.search
      )
        .then(response => {
          if (response && response.success) {
            this.items = response.data;
          }
        })
        .catch(err => {
          if (err.response && err.response && !err.response.success) {
            this.$message({
              title: "WARNING!",
              message: err.response.message,
              type: "warning",
              duration: 2000
            });
          }
        });
    }, 1000)
  }
};
</script>

