<script>
import { getWithContentWrap } from "../../../helpers/helpers";

export default {
  name: "CompanyEdit",
  data() {
    return {
      company: [],
      drawerRight: false,
      nameRules: [v => !!v || "Name is required"],
      descriptionRules: [v => !!v || "Description is required"],
      phoneRules: [
        v =>
          /^([0-9]*|\d*\.\d{1}?\d*)$/.test(v) ||
          "Phone is required and is number"
      ],
      rules: {
        required: value => !!value || "This field is required."
      },
      alertType: "success",
      alertMes: "",
      key: 0,
      alertStt: false,
      companyName: "",
      width: 0,
      drawerHeaderStt: null
    };
  },
  computed: {
    widthComputed() {
      return this.width;
    }
  },
  mounted() {
    this.$root.$on("sendEventCompanyEdit", response => {
      this.drawerRight = response.showNavigation;
      this.company = response.data;
      this.companyName = response.data.name;
      this.width = this.getCurrentWithContentWrap();
    });

    this.$root.$on("drawer-status", res => {
      this.drawerHeaderStt = res;
    });
  },

  methods: {
    getCurrentWithContentWrap() {
      return getWithContentWrap(this.drawerHeaderStt);
    },
    unDisableItem(key) {
      this.key = key;
    },
    checkValue() {
      if (
        this.company.name == "" ||
        this.company.address == "" ||
        this.company.fullname == "" ||
        this.company.description == "" ||
        this.company.phone == ""
      ) {
        this.alertStt = true;
        this.alertType = "error";
        this.alertMes = "System Error Occurred";
        setTimeout(() => {
          this.alertStt = false;
        }, 1500);
        this.$root.$emit("editCompanySuccess");
        this.key = 0;
        return false;
      }
      return true;
    },

    updateCompany(field, value) {
      let params = {};

      switch (field) {
        case "name":
          params = { name: this.company.name };
          break;
        case "fullname":
          params = { fullname: this.company.fullname };
          break;
        case "address":
          params = { address: this.company.address };
          break;
        case "description":
          params = { description: this.company.description };
          break;
        default:
          params = { phone: this.company.phone };
      }

      if (this.checkValue()) {
        axios
          .put("companies/" + this.company.id, { params: params })
          .then(response => {
            if (response && response.success) {
              this.company = response.data;
              console.log(response.data);
              this.alertStt = true;
              this.alertType = "success";
              this.alertMes = "Update Successfully";
              setTimeout(() => {
                this.alertStt = false;
              }, 1500);
              this.key = 0;
              this.$root.$emit("editCompanySuccess");

              // setTimeout(function(){
              // 	Vue.notify({
              // 	 group: 'loggedIn',
              // 	 type: 'success',
              // 	 text: 'Update success'
              // 	});
              // },500);
            }
          })

          .catch(e => {
            this.alertStt = true;
            this.alertType = "error";
            this.alertMes = "System Error Occurred";
            setTimeout(() => {
              this.alertStt = false;
            }, 1500);
            this.$root.$emit("editCompanySuccess");
            this.key = 0;
          });
      }
    }
  }
};
</script>
