<template>
  <v-navigation-drawer
    v-model="drawer"
    right
    fixed
    temporary
    :width="widthComputed"
    @input="loadDataAfterCouponCodeEvent"
  >
    <v-card class="h-100 position-relative">
      <!-- Toolbar -->
      <v-toolbar>
        <v-toolbar-title class="text-capitalize">{{ typeEvent }}</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-side-icon @click="closeDrawer">
          <v-icon>fas fa-times</v-icon>
        </v-toolbar-side-icon>
      </v-toolbar>
      <v-divider class="mt-0 mb-0"></v-divider>
      <!-- End Toolbar -->

      <v-row class="flexible-list-title">
        <v-col cols="12">
          <v-card>
            <v-list two-line>
              <template v-for="(couponCode, index) in couponCodes">
                <v-list-item
                  :key="couponCode.name"
                  ripple
                  @click="toggle(index, couponCode.id)"
                  v-if="couponCode.active"
                >
                  <v-list-item-content>
                    <v-list-item-title>
                      <v-tooltip top>
                        <span slot="activator">{{ couponCode.name }}</span>
                        <span>{{ couponCode.name }}</span>
                      </v-tooltip>
                    </v-list-item-title>
                    <v-list-item-sub-title>{{ couponCode.discount }} %</v-list-item-sub-title>
                  </v-list-item-content>

                  <v-list-item-action>
                    <v-tooltip bottom v-if="selected.indexOf(index) < 0">
                      <v-icon slot="activator">fas fa-check</v-icon>
                      <span>Add</span>
                    </v-tooltip>

                    <v-tooltip bottom v-else>
                      <v-icon color="green darken-1" slot="activator">fas fa-check</v-icon>
                      <span>Cancel Add</span>
                    </v-tooltip>

                    <v-list-item-action-text>{{ couponCode.from_date | moment("DD/MM/YYYY") }} - {{ couponCode.to_date | moment("DD/MM/YYYY") }}</v-list-item-action-text>
                  </v-list-item-action>
                </v-list-item>
                <v-divider v-if="index + 1 < couponCodes.length && couponCode.active" :key="index"></v-divider>
              </template>
            </v-list>
          </v-card>
        </v-col>
      </v-row>
    </v-card>
  </v-navigation-drawer>
</template>

<script>
import { get, post } from "../../../api";
import { getWithContentWrap } from "../../../helpers/helpers";
import Vue from "vue";

export default {
  name: "CouponCode",
  props: ["typeEvent", "item"],
  data() {
    return {
      drawer: false,
      width: 0,
      drawerHeaderStt: null,
      couponCodes: [],
      selected: [],
      couponId: null
    };
  },
  methods: {
    getCurrentWithContentWrap() {
      return getWithContentWrap(this.drawerHeaderStt);
    },
    fetchData() {
      let url = "coupon_codes";
      get(url)
        .then(res => {
          if (res && res.success) {
            this.couponCodes = res.data;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    closeDrawer() {
      this.drawer = false;
      this.$root.$emit("load-data-after-coupon-code_event");
    },
    toggle(index, id) {
      const i = this.selected.indexOf(index);

      if (i > -1) {
        this.selected.splice(i, 1);
        this.couponId = 0;
        this.cancelCouponCode(this.couponId, this.item.id);
      } else {
        if (this.selected.length > 0) {
          this.$message({
            showClose: true,
            message: "Warning, Maximum one voucher is added.",
            type: "warning"
          });
        } else {
          this.selected.push(index);
          this.couponId = id;
          this.addCouponCode(this.couponId, this.item.id);
        }
      }
    },
    addCouponCode(couponId, companyId) {
      let url = "add-coupon-code/" + couponId + "/" + companyId;
      post(url)
        .then(res => {
          if (res && res.success) {
            this.$message({
              showClose: true,
              message: "Success, " + res.message,
              type: "success"
            });
          }
        })
        .catch(err => {
          this.$message({
            showClose: true,
            message: "Error, " + res.message,
            type: "error"
          });
        });
    },
    cancelCouponCode(couponId, companyId) {
      let url = "cancel-coupon-code/" + couponId + "/" + companyId;
      post(url)
        .then(res => {
          if (res && res.success) {
            this.$message({
              showClose: true,
              message: "Success, " + res.message,
              type: "success"
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    showExistCoupon(item) {
      if (item.coupon_codes_id) {
        var _this = this;
        _.forEach(_this.couponCodes, function(value, key) {
          if (item.coupon_codes_id == value.id) {
            _this.selected.push(key);
          }
        });
      }
    },
    loadDataAfterCouponCodeEvent() {
      this.$root.$emit("load-data-after-coupon-code_event");
    }
  },
  mounted() {
    this.$root.$on("drawer-status", res => {
      this.drawerHeaderStt = res;
    });

    this.$root.$on("show-coupon-code-component", res => {
      this.selected = [];
      this.drawer = true;
      this.width = this.getCurrentWithContentWrap();
      this.showExistCoupon(res);
    });
  },
  computed: {
    widthComputed() {
      return this.width;
    }
  },
  created() {
    this.fetchData();
  }
};
</script>

