import {
    Line
} from 'vue-chartjs';
import config from '../../../config';
import Vue from 'vue';
import moment from "moment";
import {
    ChartConfig
} from "../../../constants/chart-config";
import {
    hexToRgbA
} from "../../../helpers/helpers";
import {
    get,
    getWithData
} from '../../../api';


export default {
    extends: Line,
    data() {
        return {
            dataUsd: [],
            dataIdr: [],
            dataEuro: [],
            dataTry: [],
            labels: [],
            chooes: '',
            options: {
                responsive: true,
                maintainAspectRatio: false,
                scales: {
                    yAxes: [{
                        ticks: {
                            beginAtZero: true,
                            display: false
                        },
                        gridLines: {
                            display: false,
                            drawBorder: false,
                            drawTicks: false
                        }
                    }],
                    xAxes: [{
                        ticks: {
                            display: true,
                            beginAtZero: true
                        },
                        gridLines: {
                            display: false,
                            drawBorder: false
                        }
                    }]
                },
                legend: {
                    display: false
                },
                tooltips: {
                    mode: 'index',
                    intersect: false,
                    position: 'nearest'
                }
            },
            data: {},
            company_id: JSON.parse(localStorage.getItem('user')).company_id,
        }
    },
    created() {
        this.fetchData();

    },

    mounted() {

        this.$root.$on('companyChart', res => {
            this.getData(res.params);
            this.chooes = res.chooes;
        });
    },

    methods: {
        renderChartData(labels, dataUsd, dataIdr, dataEuro, dataTry) {
            this.data = {
                labels: labels,
                datasets: [
                    {
                        label: 'USD',
                        borderColor: '#3da3e8',
                        pointBackgroundColor: '#3da3e8',
                        backgroundColor: 'transparent',
                        data: dataUsd
                    },
                    {
                        label: 'IDR',
                        borderColor: '#51c0bf',
                        pointBackgroundColor: '#51c0bf',
                        backgroundColor: 'transparent',
                        data: dataIdr
                    },
                    {
                        label: 'EURO',
                        borderColor: '#fecc60',
                        pointBackgroundColor: '#fecc60',
                        backgroundColor: 'transparent',
                        data: dataEuro
                    },
                    {
                        label: 'TRY',
                        borderColor: '#fd6585',
                        pointBackgroundColor: '#fd6585',
                        backgroundColor: 'transparent',
                        data: dataTry
                    }
                ]
            };
            this.renderChart(this.data, this.options);

        },

        fetchData() {
            let params = {
                general: 'default',
            }

            this.getData(params);
        },

        getData(params) {
            let url = 'super-admin-chart-report';
            // params.company_id = this.company_id;
            getWithData(url, params)
                .then((res) => {
                    if (res.success && res) {
                         {
                             var totalIdr = 0;
                             var totalUsd = 0;
                             var totalEuro = 0;
                             var totalTry = 0;
                            _.forEach(res.data, function (value, key) {
                                totalUsd = totalUsd + parseFloat(value.totalUsd);
                                totalIdr = totalIdr + parseFloat(value.totalIdr);
                                totalEuro = totalEuro + parseFloat(value.totalEuro);
                                totalTry = totalTry + parseFloat(value.totalTry);
                            });
                             totalUsd = totalUsd.toFixed(3)
                             totalIdr = totalIdr.toFixed(2);
                             totalEuro = totalEuro.toFixed(2);
                             totalTry = totalTry.toFixed(2);
                             this.$root.$emit('totalTransaction', {
                                 totalUsd: totalUsd,
                                 totalIdr: totalIdr,
                                 totalEuro: totalEuro,
                                 totalTry: totalTry
                             });
                            this.handleDataDaily(res.data);
                            this.getStartTimeAndEndTimeWithValue(res.data, this.chooes)
                        }
                    }

                })
                .catch((err) => {
                    console.log(err)
                });
        },

        getStartTimeAndEndTimeWithValue(obj, typeTime) {
            var keys, firstKey, lastKey
            keys = Object.keys(obj)
            firstKey = Object.keys(obj)[0]
            lastKey = keys[keys.length - 1]
            var timeObj = {
                "firstTime": firstKey,
                "lastTime": lastKey,
                "typeTime": typeTime
            }
            this.$root.$emit('load-time-in-menu-filter', timeObj)
        },

        handleDataDaily(data) {
            var lables = [];
            var totalUsd = [];
            var totalIdr = [];
            var totalEuro = [];
            var totalTry = [];
            _.forEach(data, function (value, key) {
                lables.push(key)
                totalUsd.push(value.totalUsd);
                totalIdr.push(value.totalIdr);
                totalEuro.push(value.totalEuro);
                totalTry.push(value.totalTry);
            });

            this.renderChartData(lables, totalUsd, totalIdr, totalEuro, totalTry);
        },

    }
}
