<template>
    <v-tour name="myTour" :steps="steps"></v-tour>
</template>

<script>
    export default {
        name: "PageTour",
        props: {
            DownloadExist: {
                default: true,
                type: Boolean
            }
        },
        data () {
            return {
                steps: []
            }
        },
        mounted: function () {
            this.$tours['myTour'].start()
        },
        created: function () {
            console.log('props.downloadexist', this.DownloadExist);
            this.steps = [
                {
                    target: '[data-v-step="1"]',  // We're using document.querySelector() under the hood
                    content: 'Hi! Let me assist shortly.<br>Here Download all images.<br>You will receive all in zip folder.'
                },
                {
                    target: '[data-v-step="2"]',
                    content: 'Here is download Images<br>one by one option'
                },
                {
                    target: '[data-v-step="3"]',
                    content: 'You got offer to purchase all rest files!',
                    params: {
                        placement: 'top'
                    }
                },
                {
                    target: '[data-v-step="4"]',
                    content: 'Your Photos available for <br>single purchase! <br>Take what do you like <br>before expired',
                    params: {
                        placement: 'top'
                    }
                },
            ];
            if(this.DownloadExist === false) {
                this.steps.splice(1,1);
            }
        }
    }
</script>

