<script>
    import { get } from '../../../api/index.js';

    export default {

        name: 'Header',
        data() {
            return {
                dialog: false,
                dialog2: false,
                purchaseDetailDialog: false,
                VoucherDialog: false,
                FeedbackDialog: false,
                ExpiryDialog: false,
                showDetail: true,
                rating: 0,
                review: '',
                icon: 'keyboard_arrow_down',
                customer: JSON.parse(localStorage.getItem('user')),
                imageSelected: [],
                countBasket: 0,
                dir: '',
                total: ''
            };
        },
        mounted() {
            this.$root.$on('sendEventCountBasket', res => {
                this.countBasket = res;
                // this.showImageSelected()
            });
        },
        created() {
            // this.showImageSelected()

        },
        methods: {
            showPackage() {
                this.dialog = true;
            },
            downloadPhoto() {
                this.dialog2 = true;
            },
            showPurchaseDetail() {
                this.dialog = false;
                this.purchaseDetailDialog = true;
            },
            showVoucherDialog() {
                this.dialog = false;
                this.VoucherDialog = true;
            },
            showFeedbackDialog() {
                this.dialog = false;
                this.FeedbackDialog = true;
            },
            showExpiryDialog() {
                this.dialog = false;
                this.ExpiryDialog = true;
            },
            showOrNotShowDetail() {
                this.showDetail = this.showDetail ? false : true;

                this.icon = this.icon === 'keyboard_arrow_down' ? 'keyboard_arrow_up' : 'keyboard_arrow_down';
            },
            closeDialogDownload() {
                this.dialog2 = false;
                this.showDetail = true;
                this.icon = 'keyboard_arrow_down';
            },
            showBasket() {
                this.$root.$emit('showBasket');
            },
            showImageSelected() {
                get('customer/list-image-selected?roomId=' + this.customer.room_id)
                    .then(res => {
                        if (res.data && res.data.success) {
                            this.imageSelected = res.data.data[0];
                            this.dir = res.data.data[1];
                            this.countBasket = this.imageSelected.length;
                            this.total = _.sumBy(this.imageSelected, function (v) {
                                return parseInt(v.price);
                            });
                        }
                    });
            },
            copyCode() {
                console.log('code', this.$refs.discountCode);
                let range;
                range = document.createRange();
                range.selectNode(this.$refs.discountCode);
                window.getSelection().removeAllRanges();
                window.getSelection().addRange(range);
                let successful = document.execCommand("copy");
                if (successful) {
                    alert('Discount Code was copied to clipboard!');
                }
            }
        }
    };
</script>

