<script>
import { get, post, put, del, getWithData } from "../../../api/index.js";
import { getWithContentWrap } from "../../../helpers/helpers";

export default {
  name: "CustomerEdit",

  data() {
    return {
      drawerRight: false,
          customer: [],
      rules: {
        required: value => !!value || "This field is required."
      },
      valid: true,
      alertStt: false,
      alertType: "success",
      alertMes: "",
      key: 0,
      status: ["Active", "Inactive"],
      selectStatus: "",
      imageName: "",
      imageUrl: "",
      imageFile: "",
      dialog: "",
      width: 0,
      drawerHeaderStt: null
    };
  },
  computed: {
    widthComputed() {
      return this.width;
    }
  },
  mounted() {
    this.$root.$on("drawer-status", res => {
      this.drawerHeaderStt = res;
    });

    this.$root.$on("showFormCustomerDetail", res => {
        console.log('there')
      this.drawerRight = res.showNavigation;
      this.customer = res.data;
      if (res.data.status == 1) {
        this.selectStatus = "Active";
      } else {
        this.selectStatus = "Inactive";
      }
      this.width = this.getCurrentWithContentWrap();
    });
  },
  methods: {
    getCurrentWithContentWrap() {
      return getWithContentWrap(this.drawerHeaderStt);
    },
    unDisableItem(index) {
      return getWithContentWrap(this.drawerHeaderStt);
    },
    pickFile() {
      this.$refs.image.click();
    },
    onFilePicked(e) {
      e.preventDefault();
      var file = e.target.files;

      if (file[0] !== undefined) {
        this.imageName = file[0].name;
        if (this.imageName.lastIndexOf(".") <= 0) {
          return;
        }

        const fr = new FileReader();
        fr.readAsDataURL(file[0]);
        fr.addEventListener("load", () => {
          this.imageUrl = fr.result;
          this.imageFile = file[0]; // this is an image file that can be sent to server...
        });

        var fd = new FormData();
        fd.append("_method", "PATCH");
        fd.append("avatar", file[0]);

        axios
          .post("company/branches/customer/" + this.customer.id, fd)
          .then(response => {
            if (response && response.success) {
              this.alertType = "success";
              this.alertMes = response.message;
              this.$notify({
                title: "Success",
                message: this.alertMes,
                type: this.alertType,
                duration: 2000
              });
              this.key = 0;
              this.$root.$emit("reloadTableCustomer");
            }
          })
          .catch((e, value) => {
            this.alertType = "error";
            this.alertMes = "Please upload with file image";
            this.$notify({
              title: "Success",
              message: this.alertMes,
              type: this.alertType,
              duration: 2000
            });
            this.$root.$emit("reloadTableCustomer");
            this.key = 0;
          });
      } else {
        this.imageName = "";
        this.imageFile = "";
        this.imageUrl = "";
      }
    },
    checkValue() {
      if (
        this.customer.name == "" ||
        this.customer.address == "" ||
        this.customer.status == ""
      ) {
        this.alertType = "error";
        this.alertMes = "Please type text";
        this.$notify({
          title: "Error",
          message: this.alertMes,
          type: this.alertType,
          duration: 2000
        });
        this.$root.$emit("reloadTablePhotographer");
        this.key = 0;
        return false;
      }
      return true;
    },
    editCustomer(field, value) {
      let params = {};

      switch (field) {
        case "name":
          params = { name: value };
          break;
        case "email":
          params = { email: value };
          break;
        case "address":
          params = { address: value };
          break;
        case "status":
          params = { status: value };
          break;
        default:
      }

      if (this.checkValue()) {
        axios
          .patch(
            "company/branches/customer/" + this.customer.id,
            { params: params }
          )
          .then(response => {
            if (response && response.success) {
              this.alertType = "success";
              this.alertMes = response.message;
              this.$notify({
                title: "Success",
                message: this.alertMes,
                type: this.alertType,
                duration: 2000
              });
              this.key = 0;
              this.$root.$emit("reloadTableCustomer");
            }
          })
          .catch(e => {
            this.alertType = "error";
            this.alertMes = e.message;
            this.$notify({
              title: "Error",
              message: this.alertMes,
              type: this.alertType,
              duration: 2000
            });
            this.$root.$emit("reloadTableCustomer");
            this.key = 0;
          });
      }
    },
    stopdrawerRight() {
      this.drawerRight = false;
      this.key = 0;
      this.imageUrl = "";
      this.$refs.image.value = "";
    }
  },
  computed: {
    widthComputed() {
      return this.width;
    }
  }
};
</script>

