<template>
	<v-navigation-drawer
		v-if="role_id === '2'"
		fixed
		v-model="eWalletSidebar"
		right
		temporary
		app
		clipped
		:width='widthComputed'
	>
		<v-card class="h-100 position-relative">
			<v-toolbar tabs>
		    <v-toolbar-title class="text-capitalize">E-Wallet ({{currency.symbol}}) 0</v-toolbar-title>
		    <v-spacer/>
		    <v-toolbar-side-icon @click.stop="closeDrawerRight()">
	      	<v-icon>
	          fas fa-times
	        </v-icon>
		    </v-toolbar-side-icon>
			<v-tabs
	            slot="extension"
	            v-model="tab"
	            color="primary"
	            grow
	            dark
	            class="custom-tab"
	            active-class="body-2 font-weight-bold blue darken-4 text-uppercase"
	        >
	        <v-tab
	          v-for="t in tabs"
	          :key="t.main"
	        >   <div>
                <span>{{t.main}}</span><br/>
                <span style="text-transform: none;">{{t.sub}}</span>
                </div>
	        </v-tab>
	      </v-tabs>
	    </v-toolbar>

      <v-tabs-items v-model="tab" >
	      <v-tab-item
	        v-for="t in tabs"
	        :key="t.main"
	      >
	      	<div v-if="t.main === 'HISTORY'">
						<e-wallet-transactions-history/>
	      	</div>
	      	<div v-if="t.main === 'TOP UP'">
						<top-up :currency="currency"/>
	      	</div>
	      	<div v-if="t.main === 'WITHDRAW'">
						<with-draw-via-bank/>
	      	</div>
	      </v-tab-item>
	    </v-tabs-items>
		</v-card>
	</v-navigation-drawer>
</template>

<script>
import { get } from "../../../api/index.js";
import { getWithContentWrap } from '../../../helpers/helpers.js';
import EWalletTransactionsHistory from './EWalletComponents/EWalletTransactionsHistory.vue';
import TopUp from './EWalletComponents/TopUp.vue';
import WithDrawViaBank from './EWalletComponents/WithDrawViaBank.vue';

export default {

  name: 'EWallet',
  components:{
  	EWalletTransactionsHistory,
  	TopUp,
  	WithDrawViaBank
  },
  data () {
    return {
    	eWalletSidebar: false,
    	role_id : '',
    	drawerHeaderStt: null,
    	width: 0,
    	tabs: [
            {main: 'HISTORY', sub: 'Transaction'},
            {main: 'TOP UP', sub: 'Balance'},
            {main: 'WITHDRAW', sub: 'Via Bank'}
	    ],
	    tab: null,
		currency: []
    };
  },
  mounted(){
  	this.$root.$on('open-EWallet', res => {
        this.eWalletSidebar = res.showDialog;
  		this.role_id = res.role_id;
  		this.width = this.getCurrentWithContentWrap();
    });
  },
  methods:{
		getCurrentWithContentWrap(){
  		return getWithContentWrap(this.drawerHeaderStt);
  	},
  	closeDrawerRight(){
			this.eWalletSidebar = false;
		},
		fetchCurrency() {
		  get("currency/" + this.$store.getters.getUser.currency_id)
			.then(res => {
			  if (res && res.success) {
				console.log(res.data);
				this.currency = res.data;
			  }
			})
			.catch(e => {
			  console.log(e);
			});
    },
  },
  computed: {
  	widthComputed(){
  		return this.width;
  	}
  },
	created() {
		this.fetchCurrency();
	}
};
</script>

