<template>
    <div class="px-2" style="background-color: #f5f5f5">
        <v-container fluid mx-auto pa-0>
            <v-row class="align-items-center justify-space-between mx-0" v-if="$route.path === '/shop/dashboard'">
                <div>
                    <span v-if="$vuetify.breakpoint.smAndUp">{{ branchName }}</span>
                    <span v-else>{{ branchName.slice(0,7) + '...' }}</span>
                </div>
                <div class="d-flex">
                    <overflow-selection :items="currencies"></overflow-selection>
                    <overflow-selection :items="languages"></overflow-selection>
                    <v-btn fab text medium @click="logout" class="ma-0">
                        <v-img src="/images/logout.png" contain width="40px" height="30px"></v-img>
                    </v-btn>
                </div>
            </v-row>

            <v-row class="align-items-center" v-else>
                <v-btn large icon @click="$router.push('/shop/dashboard')">
                    <v-icon class="font-2x" style="color: #70778F">arrow_back</v-icon>
                </v-btn>
                <v-spacer/>
                <v-toolbar-items>
                    <v-btn text @click="dialog = true" class="custom-header-btn px-1">
                        <i class="fa fa-gift font-material-header" aria-hidden="true"/>
                        <span>{{$t('message.albumPackage')}}</span>
                    </v-btn>
                    <v-btn text class="custom-header-btn px-1" @click="snap.dialog = true">
                        <i class="material-icons font-material-header">add_a_photo</i>
                        <span>{{$t('message.addNewPhoto')}}</span>
                    </v-btn>
                </v-toolbar-items>
            </v-row>
        </v-container>
        <v-dialog v-model="dialog" content-class="v-dialog-package" temporary>
            <v-container fluid grid-list-xl pt-0 class="container-package-ramdom pos-relative">
                <v-btn fab small flat absolute right class="mr-n3 mt-n5 mb-0" @click="dialog = false">
                    <v-icon>close</v-icon>
                </v-btn>
                <div class="pricing-wrapper">
                    <v-row>
                        <template v-for="(value, key) in packages">
                            <v-col cols="12" sm="12" md="3" lg="3" xl="3" :key="key">
                                <div class="container-package-ramdom--flex">
                                    <div class="app-card text-center">
                                        <div
                                            class="app-card-title d-custom-flex justify-space-between"
                                        >
                                            <h3 class="mb-0 package-name">{{value.package_name}}</h3>
                                        </div>

                                        <div class="app-full-content">
                                            <h2 class="font-3x py-2">
                                                <span class="font-xl">{{formatPrice(value.dollar)}}$</span>
                                                <!--                                  <sub>{{value.offer}}</sub>-->
                                            </h2>
                                            <ul class="list-unstyled list-group-flush">
                                                <li class="list-group-item">{{value.total_photo}} photos Print</li>
                                                <li class="list-group-item">{{value.album}}</li>
                                                <li class="list-group-item">{{value.photo_on_disc}} photos on the disc
                                                </li>
                                                <li
                                                    class="list-group-item"
                                                >{{key == 0 ? '.........' : ( key == 1 ? 1 + ' Poster (20x30)' : 2 + 'Poster (20x30)') }}
                                                </li>
                                            </ul>
                                        </div>
                                        <div class="app-footer">
                                            <v-btn
                                                flat
                                                large
                                                @click="choosePackage(value.package_name)"
                                            >{{$t('message.selectNow')}}
                                            </v-btn>
                                        </div>
                                    </div>
                                </div>

                            </v-col>
                        </template>
                    </v-row>
                </div>
            </v-container>
        </v-dialog>
        <v-dialog
            v-model="snap.dialog"
            width="400"
            temporary
            content-class="v-dialog-custom">
            <v-card class="v-card-room">
                <v-btn fab small flat absolute right class="mr-n3" @click="snap.dialog = false">
                    <v-icon>close</v-icon>
                </v-btn>
                <v-card-title class="justify-content-center flex-column pb-0">
                    <span style="color: #0EBFF6;font-size: 30px">Snap Photos Gallery</span>
                    <span style="color: grey;font-size: 15px">Select date to see daily snap shots</span>
                </v-card-title>
                <v-card-text class="text-center my-6">
                    <v-date-picker v-model="snap.date" no-title style="box-shadow: none"></v-date-picker>
                </v-card-text>
                <v-card-actions class="justify-content-center mb-4">
                    <v-btn color="primary" style="width: 80%; font-size: 20px" round>
                        NEXT &nbsp;<i class="fas fa-arrow-right"/>
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
    import {mapGetters} from "vuex";

    export default {
        name: "Header",

        data() {
            return {
                dialog: false,
                currency: "USD",
                language: "EN",
                currencies: ["USD", "EURO"],
                languages: ["EN", "RU", "DE", "TR", "AR", "FR", "ID"],
                branchName: JSON.parse(localStorage.getItem("user")).branch_name,
                snap: {
                    dialog: false,
                    date: new Date().toISOString().substr(0, 10)
                }
            };
        },
        created() {
            this.$i18n.locale = "en";
            this.$store.dispatch("getPackages");
        },
        computed: {
            ...mapGetters({
                packages: "packages"
            })
        },
        methods: {
            choosePackage(value) {
                alert("choose package");
                this.$root.$emit("typePackage", {name: value});
            },
            colorPackage(key) {
                let color = "";
                switch (key) {
                    case 0:
                        color = "default";
                        break;
                    case 1:
                        color = "success";
                        break;
                    case 2:
                        color = "warning";
                        break;
                    default:
                        color = "error";
                }
                return color;
            },
            logout() {
                localStorage.removeItem("shopSelling");
                localStorage.removeItem("roomLogin");
                localStorage.removeItem("photoSelected");
                localStorage.removeItem("thumbnailDir");

                this.$store.dispatch("logoutUser", {
                    redirectAfterLogout: "/shop/login"
                });
            },
            formatPrice(value) {
                return parseInt(value);
            }
        }
    };
</script>

