<script>
import { getWithData, post } from "@/api";
// import $ from "jquery";
 //import dateFormate from "https://staging.doku.com/dateformat.js"
export default {
  name: "TopUp",
  props:['currency'],
  data() {
    return {
      payment: false,
      user: JSON.parse(localStorage.getItem("user")),
      total_ewallet: 0,
      userChosse: "new_credit",
      amount: '',
      rules: {
        required: value => !!value || "Required.",
        number: value => {
          const p = /^[0-9]\d*$/;
          return p.test(value) || "Please input number format.";
        },
        decimal: value => {
          const p = /^[0-9]\d*(\.\d+)?$/;
          return p.test(value) || "Please input decimal format.";
        },
        fullname: value => {
          const p = /^[a-z \\-]*$/i;
          return p.test(value) || "Please input card name format";
        }
      },
      form: false,
      show: true,
      isLoading: false,
      label: 'Amount on '+this.currency.name
    };
  },
  methods: {
    // dokuHosted () {
    //   dokupost("https://staging.doku.com/Suite/Receive", {
    //     amount: this.amount+'.00',
    //     invoice: this.invoiceNumber,
    //     currency: this.currency.doku_currency_id
    //   })
    // },
    callWallet() {
      let params = {
        company_id: this.user.company_id,
        user_id: this.user.id
      };

      getWithData("e-wallet/total-ewallet", params)
        .then(res => {
          if (res.data) {
            let data = res.data;
            this.total_ewallet = data.toFixed(3);

            if (this.total_ewallet <= 0) {
              this.total_ewallet = 0;
            }
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    notpaidTransaction() {
      this.payment = true
      post("doku/savetransaction", {
        transidmerchant: this.invoiceNumber,
        totalamount: this.dokuAmount,
        words: this.words,
        trxstatus: 'NOTPAID'
      }).then(() => {

      }).catch(err => {
        console.log(err);
      });
    },
    choose(value) {
      this.userChosse = value;
      if (value === "new_credit") {
        this.show = true;
      }
      if (value === "visa") {
        this.show = false;
      }
    },
    rndStr() {
      let text = ""
       let chars = "ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789"

        for( let i=0; i < 10; i++ ) {
         text += chars.charAt(Math.floor(Math.random() * chars.length))
        }
       return text
    },    
  },
  computed: {
    dokuAmount () {
      let price = this.amount+'.00'
      return price
    },
    words () {
      let generate = this.dokuAmount + '5946' + 'MPHYk2r1ek92' + this.invoiceNumber
      return this.SHA1(generate)
    },
    basket () {
      return 'Topup,'+this.dokuAmount+',1,'+this.dokuAmount+';'
    },
    invoiceNumber () {
      let gencode = this.rndStr();
      let invoice = 'HOLIPIC-'+gencode;
      return invoice
    },
    getRequestDateTime() {
      let now = new Date();

      return this.dateFormat(now, "yyyymmddHHMMss");
    }
  },
  created() {
    this.callWallet();
  }
};
</script>


