import 'babel-polyfill';
import Vue from 'vue';
import App from './App.vue';
import vuetify from './plugins/vuetify';

import wysiwyg from 'vue-wysiwyg';
import VueBreadcrumbs from 'vue2-breadcrumbs';

import Notifications from 'vue-notification';
import velocity from 'velocity-animate';

import Nprogress from 'nprogress';
import VueI18n from 'vue-i18n';
import VueTour from 'vue-tour';
import fullscreen from 'vue-fullscreen';

// global components
import GlobalComponents from './globalComponents';
// router
import router from './router';

// store
import { store } from './store/store';

// messages
import messages from './lang';


//css
import 'material-design-icons-iconfont/dist/material-design-icons.css';

require('./assets/style.css');
//require('@favicon.png');
// require('./assets/libraries/animate.css');
// require('./assets/libraries/custom.css');
// require('./assets/libraries/flaticon.css');
// require('./assets/libraries/font-awesome.min.css');
// require('./assets/libraries/pages.css');
// require('./assets/libraries/pricing.css');
// require('./assets/libraries/slicknav.css');

Vue.config.productionTip = false;

// navigation guards before each
router.beforeEach((to, from, next) => {
	Nprogress.start();
	if (to.matched.some(record => record.meta.requiresAuth)) {
		// this route requires auth, check if logged in
		// if not, redirect to login page.
		if (localStorage.getItem('user') === null) {
			next({
				path: '/login',
				query: { redirect: to.fullPath }
			});
		} else {
			next();
		}
	} else {
		next(); // make sure to always call next()!
	}
});

// navigation guard after each
router.afterEach(() => {
	Nprogress.done();
	setTimeout(() => {
		const contentWrapper = document.querySelector(".v-content__wrap");
		if (contentWrapper !== null){
			contentWrapper.scrollTop = 0;
		}
		const boxedLayout = document.querySelector('.app-boxed-layout .app-content');
		if (boxedLayout !== null){
			boxedLayout.scrollTop = 0;
		}
		const miniLayout = document.querySelector('.app-mini-layout .app-content');
		if (miniLayout !== null){
			miniLayout.scrollTop = 0;
		}
	}, 200);
});


Vue.use(VueI18n);
Vue.use(VueTour);
Vue.use(wysiwyg, {});
Vue.use(VueBreadcrumbs);
Vue.use(Notifications, { velocity });
Vue.use(fullscreen);
Vue.use(GlobalComponents);
Vue.use(require('vue-moment'));

const i18n = new VueI18n({
	locale: store.getters.selectedLocale.locale, // set locale
	messages, // set locale messages
});


new Vue({
  store,
	i18n,
	router,
  vuetify,
  render: h => h(App),
	components: { App }
}).$mount('#app');
