                            class="flex p-sm-2 px-1 mb-1">
                            <v-responsive width="100%">
                                <div class="img-wrapper-2" @dblclick="showSildeWithImageId(index)">
                                    <div style="width: 100%; padding-top: 75%;">
                                        <v-img
