                                        </v-img>
                                    </div>

                                </div>
                            </v-responsive>
                            <div class="pt-flex justify-content-between">
                                <v-btn text icon small @click="showSildeWithImageId(index)" class="ml-0 mr-0" color="grey">
                                    <i class="fa fa-expand font-2x" aria-hidden="true"></i>
                                </v-btn>
                                <v-btn text icon small class="ml-0 mr-0"
                                       :data-v-step="index === 0 ? '4' : ''"
                                       :color="photoSelected.indexOf(photo) > -1 ? 'primary' : 'grey'"
                                       @click="photoSelected.indexOf(photo) > -1 ? '': selectPhoto(photo,index)">
                                    <!-- :class="'active-image'+photo.id" -->
