                    dark
                    prev-icon="fas fa-chevron-circle-left"
                    next-icon="fas fa-chevron-circle-right"
                    class="pt-custom-carousel"
                    @click.native="click"
                    multiple
                >
                    <v-carousel-item v-for="(item,i) in slidePhotosComputed" :key="i" @click="click">
                        <div class="display-flex justify-content-center align-items-center h-100">
                            <div class="pos-relative"
                                 :class="$vuetify.breakpoint.width > $vuetify.breakpoint.height ? 'h-100' : 'w-100'">
                                <img
                                    :src="item"
                                    :class="$vuetify.breakpoint.width > $vuetify.breakpoint.height ? 'h-100' : 'w-100'"
                                    :id="`carousel-item-${i}`">
                                <v-btn
                                    text icon light
                                    class="check-btn"
                                    :color="photoSelected.indexOf(item) > -1 ? 'primary' : 'white'"
                                    :id="`pt-icon-key-${i}`"
                                    @click="selectPhoto(item, i)">
                                    <i class="fa fa-check-circle" aria-hidden="true"/>
                                </v-btn>
                                <div class="pt-slide-show">
                                    <div class="pb-2">
                                        <v-btn text icon @click="slideShows">
                                            <v-icon
                                                x-large
                                                color="white"
                                            >far {{ slideAction ? "fa-pause-circle" : "fa-play-circle"}}
                                            </v-icon>
                                        </v-btn>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <template v-slot:placeholder>
                            <v-row fill-height align-center justify-center ma-0>
                                <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
