                        <div class="pt-close-slide display-flex align-items-center">
                            <v-icon @click="closeSlide" color="white" large>fas fa-arrow-left</v-icon>
                            <div class="pl-4">
                                <span class="h6 font-weight-bold" style="color: white; vertical-align: middle">{{photoNameComputed(item)}}</span>
