            <!--Dialog table cart-->
            <v-dialog v-model="cartDialog" scrollable content-class="custom-dialog-basket" max-width="1000">

                <table class="table text-center mb-0">
                    <thead class="table-header">
                    <tr>
                        <th class="border-top-0 px-1">Photos</th>
                        <th class="border-top-0 px-1">Product</th>
                        <th class="border-top-0 px-1">Quantity</th>
                        <th class="border-top-0 px-1">Price</th>
                        <th class="border-top-0 px-1">
                            <v-icon class="cursor-pointer black--text" @click="cartDialog = false">close</v-icon>
                        </th>
                    </tr>
