                    <tbody class="font-md align-middle">
                    <tr v-for="(image,index) in photoSelected">
                        <td><img :src="image" width="60px" height="60px"></td>
                        <td class="align-middle px-0">Digital</td>
                        <td class="align-middle px-0">1</td>
                        <td class="align-middle px-0">10$</td>
                        <td class="align-middle px-0">
                      <!--      <v-btn icon small @click="deletePhotoSelected(image,index)"
                                   style="border: 1px solid red">
                                <v-icon color="red">clear</v-icon>
                            </v-btn>
                            <br/>-->
                            <span @click="deletePhotoSelected(image,index)" class="font-sm red--text cursor-pointer">Remove</span>
                        </td>
