<template>
    <v-container fluid fill-height class="pa-0" white>
        <v-row align="center" justify="center" class="mx-0">
            <v-col cols="12" sm="8" md="6" lg="4" class="px-12">
                <app-card :customClasses="$vuetify.breakpoint.smAndUp ? 'elevation-5' : 'elevation-0'">
                    <div class="session-content text-center pa-sm-10">
                        <v-radio-group v-model="type">
                            <v-row class="justify-space-around align-items-center">
                                <v-radio label="Selling Page" color="primary" value="selling" class="mb-0"></v-radio>
                                <v-radio label="Branch Admin" color="primary" value="branch"></v-radio>
                            </v-row>
                        </v-radio-group>
                        <p class="display-2"><b>Welcome</b></p>
                        <v-img
                            :src="appLogo"
                            class="d-inline-block img-responsive mb-4 rounded"
                            width="150"
                            height="150"></v-img>
                        <v-card-text class="text-muted text-center h2 lead py-0">Login to Start Selling Photos</v-card-text>
                        <v-card-text class="pl-md-5 pr-md-5 py-0 display-flex align-items-center" style="min-height: 150px">
                            <v-form ref="form" v-model="valid" lazy-validation>
                                <v-text-field
                                    v-model="email"
                                    :rules="branchRules"
                                    label="Branch ID"
                                    required
                                    @keyup.enter="login"></v-text-field>
                                <v-text-field
                                    v-if="type==='branch'"
                                    :type="'password'"
                                    v-model="password"
                                    :rules="passwordRules"
                                    label="Password"
                                    required
                                    @keyup.enter="login"></v-text-field>
                            </v-form>
                        </v-card-text>
                        <v-card-actions class="pl-md-5 pr-md-5 pt-2 pb-12">
                            <v-btn color="primary" large @click="login" block rounded>
                                Login
                            </v-btn>
                        </v-card-actions>
                    </div>
                </app-card>
            </v-col>
        </v-row>
    </v-container>
</template>
<script>
    import AppConfig from "../../constants/AppConfig";

    export default {
        name: "Login",

        data() {
            return {
                appLogo: AppConfig.appLogo,
                type: 'selling',
                valid: true,
                email: "",
                password: "",
                passwordRules: [
                    v => !!v || "Password is required",
                    v => (v && v.length <= 10) || "Password must be less than 10 characters"
                ],
                branchRules: [
                    v => !!v || "Branch ID is required"
                ]
            };
        },
        methods: {
            login() {
                if (this.$refs.form.validate()) {
                  if (this.type === 'selling') {
                    const user = {
                      email: this.email,
                      password: "123456",
                      // role_id: "5"
                    }

                    this.$store.dispatch("signinUser", { user })
                  }
                  else {
                    const user = {
                      email: this.email,
                      password: this.password,
                      // role_id: "3"
                    }

                    this.$store.dispatch("signinUser", { user })
                  }
              }
            }
        }
    };
</script>

