<template>
  <div>
    <v-card flat>
      <v-card-text class="text-muted text-center h2 lead">
        Login to Manage Whole Bussiness
      </v-card-text>
      <v-form v-model="valid" class="mb-6" ref="form">
        <v-text-field
          label="E-mail"
          v-model="email"
          :rules="emailRules"
          required
          @keyup.enter="login"
        ></v-text-field>

        <v-text-field
          label="Password"
          v-model="password"
          type="password"
          :rules="passwordRules"
          required
          @keyup.enter="login"
        ></v-text-field>
        <v-row class="justify-space-between mx-0">
          <v-checkbox color="primary" label="Remember me" v-model="checkbox"></v-checkbox>
          <router-link style="color:rgb(121, 121, 121);" class="mt-6 subtitle-1" to="/session/forgot-password">{{ $t('message.forgotPassword') }}?</router-link>
        </v-row>
        <div>
          <v-btn large @click="login" block color="primary">{{ $t('message.loginNow') }}</v-btn>
        </div>
      </v-form>
    </v-card>
  </div>
</template>

<script>

export default {
  name: "Login",
  data() {
    return {
      valid: true,
      lazy: true,
      checkbox: false,
      password: "",
      email: "",
      emailRules: [
        v => !!v || "E-mail is required",
        v =>
          /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) ||
          "E-mail must be valid"
      ],
      passwordRules: [v => !!v || "Password is required"]
    };
  },
  methods: {
    submit() {
      const user = {
        email: this.email,
        password: this.password
      };

      this.$router.push({
        path: "/default/dashboard/ecommerce"
      });
    },

    login() {
      if (this.$refs.form.validate()) {
        const user = {
          email: this.email,
          password: this.password
        };
        this.$store.dispatch("signinUser", { user });
      }
    }
  }
};
</script>

