<template>
  <v-container fluid>
    <app-card>
      <v-navigation-drawer
        fixed
        v-model="drawer"
        :right="!rtlLayout"
        temporary
        app
        :width="widthComputed"
      >
        <coupon-code-item :eventType="eventType" :item="item"/>
      </v-navigation-drawer>

      <!-- <page-title-bar></page-title-bar> -->

      <v-card-title flat color="white" class="py-2">
        <span class="headline">Coupons</span>
        <v-spacer/>
        <input type="search" v-model="search" class="cari" id="search" name="search" placeholder="Search">
<!--        <v-tooltip bottom>-->
          <v-btn
            @click="couponCodeEvent('add-new', null)"
            class="btn-add"
            color="#ededed"
            fab
            icon
            small
          >
            <v-icon dark>fas fa-plus</v-icon>
          </v-btn>
<!--        </v-tooltip>-->
      </v-card-title>

      <v-data-table
        :headers="headers"
        :items="desserts"
        class="elevation-5"
        :pagination.sync="pagination"
        :loading="loading"
        item-key="id"
        :search="search"
        :rows-per-page-items="rowsPerPageItems"
      >
        <v-progress-linear slot="progress" color="blue" indeterminate></v-progress-linear>

        <!--Header -->
        <template slot="headers" slot-scope="props">
          <tr>
            <th
              v-for="header in props.headers"
              :key="header.text"
              :class="['column sortable ', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
              @click="changeSort(header.value)"
            >
              <div class="custom-header text-center">
                <v-tooltip bottom>
                  <span
                    slot="activator"
                    class="text-capitalize font-weight-bold"
                  >{{ header.text }}</span>
                  <span>{{ header.text }}</span>
                </v-tooltip>
                <v-icon v-if="header.value != 'actions' && header.value != 'code'">arrow_upward</v-icon>
              </div>
            </th>
          </tr>
        </template>
        <!--Prop data -->
        <template v-slot:item="{ item }">
            <tr>
                <td>{{ item.name }}</td>
                <td>{{ item.code }}</td>
                <td>{{ item.discount_type }}</td>
                <td>{{ item.discount }}</td>
                <td>{{ '1'}}</td>
                <td>{{ item.to_date | moment("DD/MM/YYYY") }}</td>
                <td>{{ item.recipient }}</td>
                <td v-if="item.active == '0' ">Inactive</td>
                <td v-if="item.active == '1' ">Active</td>
                <td>
                    <v-icon small class="mr-2" @click="couponCodeEvent('edit', item)">edit</v-icon>
                    <v-icon small @click="showDialog(item.id)">delete</v-icon>
                </td>
            </tr>
        </template>

        <!--No data -->
        <template slot="no-data">
          <v-alert color="error" icon="warning">Sorry, nothing to display here :(</v-alert>
        </template>

        <!--Search no result -->
        <v-alert
          slot="no-results"
          :value="true"
          color="error"
          icon="warning"
        >Your search for "{{ search }}" found no results.</v-alert>
      </v-data-table>
    </app-card>
       <delete-confirmation-dialog ref="deleteConfirmationDialog" @onConfirm="deleteItem">
      </delete-confirmation-dialog>
  </v-container>
</template>

<script>
import { get, del } from "../../../api/index.js";
import Vue from "vue";
import CouponCodeItem from "./CouponCodeItem";
import moment from "moment";
import { mapGetters } from "vuex";
import { getWithContentWrap } from "../../../helpers/helpers";

export default {
  name: "CouponCodes",

  data() {
    return {
      search: "",
      desserts: [],
      headers: [
        { text: "Name", value: "name", width: "20%" },
        { text: "Code", value: "code", width: "10%", sortable: false },
        { text: "Disc.Type", value: "discount_type", width: "10%" },
        { text: "Value", value: "discount", width: "10%" },
        { text: "Usage", value: "usage", width: "15%" },
        { text: "Expiry", value: "to_date", width: "15%" },
        { text: "Recipient", value: "recipient", width: "15%" },
        { text: "Active", value: "active" },
        { text: "Actions", value: "actions", sortable: false }
      ],
      pagination: {},
      loading: true,
      search: "",
      rowsPerPageItems: [
        10,
        25,
        50,
        100,
        { text: "$vuetify.dataIterator.rowsPerPageAll", value: -1 }
      ],
      drawer: false,
      item: null,
      eventType: "",
      dialog: false,
      itemIdToDelete: 0
    };
  },
  created() {
    this.loading = true;
    this.fetchData();
  },
  methods: {
    changeSort(column) {
      var columnsNoSearch = ["code", "actions"];
      if (columnsNoSearch.indexOf(column) > -1) {
        return;
      }
      this.loading = true;
      if (this.pagination.sortBy === column) {
        this.pagination.descending = !this.pagination.descending;
      } else {
        this.pagination.sortBy = column;
        this.pagination.descending = false;
      }
      this.loading = false;
    },
    fetchData() {
      let url = "coupon_codes";
      get(url)
        .then(res => {
          if (res && res.success) {
            this.desserts = res.data;
            this.loading = false;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    couponCodeEvent(event, item) {
      this.drawer = true;
      this.$root.$emit("disabled-coupon-code-item");
      this.eventType = event;
      if (this.eventType === "add-new") {
        this.$root.$emit("reset-form");
      }
      this.item = item;
    },
    deleteItem() {
      let url = "coupon_codes/" + this.itemIdToDelete;
      del(url)
        .then(res => {
          this.dialog = false;
          this.fetchData();
          setTimeout(function() {
            Vue.notify({
              type: "success",
              text: "Delete Item Success!"
            });
          }, 1000);
        })
        .catch(err => {
          console.log(err);
        });
    },
    showDialog(id) {
      this.$refs.deleteConfirmationDialog.openDialog();
      this.itemIdToDelete = id;
    }
  },
  components: {
    "coupon-code-item": CouponCodeItem
  },
  mounted() {
    this.$root.$on("closeDrawerItem", res => {
      this.drawer = res;
    });

    this.$root.$on("reload-table", res => {
      this.fetchData();
    });

    this.$root.$on("editItemSucess", res => {
      this.loading = true;
      this.fetchData();
    });
  },
  computed: {
    ...mapGetters(["rtlLayout"]),
      widthComputed() {
          return getWithContentWrap();
      }
  }
};
</script>

<style lang="scss" scoped>
    .v-btn.btn-add {
        width: 40px!important;
        height: 40px!important;
    }
</style>
