import Vue from 'vue';
import Router from 'vue-router';

/////////////////////////////////////////////////////////////////////
////////////////////////////  ROUTES  ///////////////////////////////
/////////////////////////////////////////////////////////////////////
import {
    BaseAuth,
    CustomerAuth,
    ShopAuth
} from './auth';
import SuperAdminRoutes from './super-admin';
import CompanyAdminRoutes from './company-admin';
import BranchAdminRoutes from './branch-admin';
import CustomerRoutes from './customer';
import ShopSellingRoutes from './shop';

import PageNotFound from '../views/partials/pages/page404';
import Test from '../views/test';

Vue.use(Router);

const routers = [
    BaseAuth,
    CustomerAuth,
    ShopAuth,
    CompanyAdminRoutes,
    ShopSellingRoutes,
    SuperAdminRoutes,
    CustomerRoutes,
    BranchAdminRoutes,
    {
        // Page Not Found
        path: '/test',
        component: Test
    },
    {
        // Page Not Found
        path: '*',
        component: PageNotFound
    }
];

const router = new Router({
    mode: 'history',
    routes: routers,
});

export default router;
