<template>
   <v-menu offset-y origin="right top" left content-class="language-dropdown" transition="slide-y-transition" nudge-top="-10">
		<template v-slot:activator="{ on }">
         <v-btn icon large v-on="on" class="user-block-wrap">
             <v-icon color="grey">more_vert</v-icon>
         </v-btn>
      </template>
		<div class="dropdown-content">
         <div class="dropdown-top white--text primary">
            <span class="white--text fs-14 fw-bold d-block">{{ user.full_name }}</span>
            <span class="d-block fs-12 fw-normal">{{ user.email }}</span>
         </div>
         <v-list class="dropdown-list">
            <template v-for="userLink in userLinks">
               <template v-if="userLink.id !== 4">
                  <v-list-item :to="getMenuLink(userLink.path)" :key="userLink.id">
                     <i :class="userLink.icon"></i>
                     <span>{{$t(userLink.title)}}</span>
                  </v-list-item>
               </template>
               <template v-else>
                  <v-list-item @click="logoutUser" :key="userLink.id">
                     <i :class="userLink.icon"></i>
                     <span>{{$t(userLink.title)}}</span>
                  </v-list-item>
               </template>
            </template>
         </v-list>
      </div>
	</v-menu>
</template>
<script>
   import { getCurrentAppLayout } from "@/helpers/helpers";

   export default {
      data() {
         return {
            userLinks: [
               {
                  id: 1,
                  title: 'message.userProfile',
                  icon: 'ti-user mr-3 primary--text',
                  path: '/users/user-profile'
               },
                {
                    id: 2,
                    title: "message.changeImage",
                    icon: "ti-book mr-3 info--text",
                    path: "/user/plans"
                },
                {
                    id: 3,
                    title: "message.changePassword",
                    icon: "ti-lock mr-3 info--text",
                    path: "/change-password"
                },
               {
                  id: 4,
                  title: 'message.logOut',
                  icon: 'ti-power-off mr-3 error--text'
               }
            ],
             user: {},
             role_id: ""
         };
      },
      methods: {
         logoutUser() {
            this.$store.dispatch("logoutUser", this.$router);
         },
         getMenuLink(path) {
            return '/' + getCurrentAppLayout(this.$router) + path;
         }
      },
       created() {
           const userAuth = JSON.parse(localStorage.getItem("user"));
           this.role_id = userAuth.role_id;
           this.user = userAuth;
       }
   };
</script>
