<script>
    import {get, post, put, del, getWithData} from "../../api/index.js";
    import {mapGetters} from "vuex";
    import vue2Dropzone from 'vue2-dropzone';
    import 'vue2-dropzone/dist/vue2Dropzone.min.css';


    export default {
        name: "Dashboard",
        components: {
            vueDropzone: vue2Dropzone
        },
        data() {
            return {
                items: [
                    {
                        src: "/images/shop/item_1.png",
                        title: "See\nPhotos"
                    },
                    {
                        src: "/images/shop/item_2.png",
                        title: "Make\nAppointment"
                    },
                    {
                        src: "/images/shop/item_3.png",
                        title: "Add\nPhotos"
                    },
                    {
                        src: "/images/shop/item_4.png",
                        title: "Snap(daily)\nPhotos"
                    }
                ],
                textChange: "",
                room: {
                    dialog: false,
                    Number: '',
                    Selected: null
                },
                booking: {
                    dialog: false,
                    name: '',
                    roomNumber: '',
                    email: '',
                    countryCode: '',
                    countryCodes: ['(+00)Russia', '(+00)Germany', '(+00)Australia', '(+00)Turkey', '(+00)Indonesia', '...'],
                    photoDate: new Date().toISOString().substr(0, 10),
                    time: null,
                    menu1: false,
                    menu2: false,
                    menu3: false,
                    photographer: '',
                    notice: ''
                },
                upload: {
                    dialog: false,
                },
                dropzoneOptions: {
                    url: 'https://httpbin.org/post',
                    thumbnailWidth: 200,
                    addRemoveLinks: true
                },
                snap: {
                    dialog: false,
                    date: new Date().toISOString().substr(0, 10)
                }

            };
        },
        computed: {
            ...mapGetters({
                shopRooms: "shopRooms"
            }),
            photoDateFormatted() {
                return this.formatDate(this.booking.photoDate);
            }
        },

        methods: {
            async makeEvent(key) {
                switch (key) {
                    case 0:
                        this.room.dialog = true;
                        break;
                    case 1:
                        this.booking.dialog = true;
                        break;
                    case 2:
                        this.upload.dialog = true;
                        break;
                    case 3:
                        this.snap.dialog = true;
                        break;
                }
            },
            typeNumberRoom(value) {
                if (value == 'remove') {
                    this.room.Number = '';
                } else if (value == 'removeOne') {
                    this.room.Number = this.room.Number.substring(0, this.room.Number.length - 1);
                } else {
                    this.room.Number = this.room.Number + value;
                }
            },
            formatDate(date) {
                if (!date) return null;
                const [year, month, day] = date.split('-');
                return `${month}/${day}/${year}`;
            },
            viewPhotos() {

                this.$router.push({
                    name: "shop-photos",
                    params: {roomId: this.room.Selected}
                });
                //   get("room/login?room=" + this.roomNumber)
                //     .then(res => {
                //       if (res && res.success) {
                //         var data = res.data;
                //         localStorage.setItem("roomLogin", JSON.stringify(data));
                //         this.$router.push({ path: "/shop/photos", params: { data: data } });
                //       }
                //     })
                //     .catch(err => {
                //       this.$notify({
                //         title: "Error",
                //         message: err.response.data.message,
                //         type: "error",
                //         duration: 2000
                //       });
                //     });
            },

            searchRoom () {
                post('checkRoom', {
                    branch_id: this.$store.getters.getUser.branch_id,
                    room_id: this.room.Number
                })
                .then((res) => {
                    if (res && res.success) {
                      // console.log(res.data);
                      if (!res.data) {
                        alert(res.message);
                      }
                      else {
                        this.$router.push({ name: 'shop-photos', params: { roomId: res.data }});
                      }
                        // context.commit('shopRooms', res.data)
                    }
                })
                .catch((err) => {
                    console.log(err);
                });
            }
        },
    };
</script>

