<template>
	<v-menu offset-y left origin="right top" z-index="99" content-class="notification-dropdown" transition="slide-y-transition" nudge-top="-10">
		<template v-slot:activator="{ on }">
			<v-btn class="notification-icon ma-0" v-on="on" icon large >
                <v-badge
                    :content="countNotifications"
                    :value="countNotifications"
                    color="primary"
                    overlap
                >
                    <i class="zmdi grey--text zmdi-notifications-active animated infinite wobble zmdi-hc-fw font-lg"></i>
                </v-badge>
			</v-btn>
		</template>
		<div class="dropdown-content">
			<div class="dropdown-top d-custom-flex justify-space-between primary">
				<span class="white--text fw-bold">Notifications</span>
				<span class="v-badge warning">{{countNotifications}} NEW</span>
			</div>
			<vue-perfect-scrollbar class="scroll-area">
				<v-list class="dropdown-list">
					<v-list-item v-for="(notification, index) in notifications" :key="index" class="flex-column">
						<i class="mr-3 zmdi" :class="notification.icon"></i>
						<span>{{ $t(notification.message) }}</span>
						<span>{{ notification.created_at }}</span>
					</v-list-item>
				</v-list>
			</vue-perfect-scrollbar>
		</div>
	</v-menu>
</template>

<script>
	import { get } from "../../api/index.js";
	export default {
		data() {
			return {
				notifications: [],
				user: JSON.parse(localStorage.getItem("user")),
			};
		},
		created() {
			this.getAllNotification();
		},
		computed: {
			countNotifications() {
				return this.notifications.length;
			}
		},
		methods: {
			getAllNotification() {
				const useAut = this.user;
				let url = "get-notifications/" + useAut.id;
				get(url)
						.then(res => {
							if (res && res.success) {
								this.notifications = res.data;
								console.log('herere', res.data);
							}
						})
						.catch(err => {
							console.log(err);
						});
			},
		}
	};
</script>
<style lang="scss" scoped>
	.scroll-area {
		height: 200px;
	}
</style>