<template>
  <v-container fluid>
    <app-card>
        <!--Search Component -->
        <v-card-title flat color="white" class="py-2">
            <span class="headline">Branches List</span>
            <v-spacer/>
            <input type="search" v-model="search" class="cari" id="search" name="search" placeholder="Enter Search Value">
                <v-btn
                    @click="showFromAdd"
                    color="#ededed" icon
                    depressed
                    slot="activator"
                    fab
                    small
                >
                    <v-icon color="grey">fas fa-plus</v-icon>
                </v-btn>
        </v-card-title>

        <!-- Table Component -->
        <v-data-table
          :headers="headers"
          :items="items"
          class="elevation-5"
          :pagination.sync="pagination"
          :rows-per-page-items="rowsPerPageItems"
          default-sort="id:desc"
          :search="search"
        >
          <v-progress-linear slot="progress" color="blue" indeterminate></v-progress-linear>
          <!--Header -->
          <template slot="headers" slot-scope="props">
            <tr>
              <th
                v-for="header in props.headers"
                :key="header.text"
                :class="['column sortable', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
                @click="changeSort(header.value)"
              >
                <div class="custom-header">
                  <v-tooltip bottom>
                    <span
                      slot="activator"
                      class="text-capitalize font-weight-bold"
                    >{{ header.text }}</span>
                    <span>{{ header.text }}</span>
                  </v-tooltip>
                  <v-icon v-if="header.value != 'actions'">arrow_upward</v-icon>
                </div>
              </th>
            </tr>
          </template>

          <template slot="items" slot-scope="props">
            <td>{{ props.item.id }}</td>
            <td class="text-xs-left">{{ props.item.name }}</td>
            <td class="text-xs-left">{{ props.item.user.username }}</td>
            <td class="text-xs-left">{{ props.item.branch_password }}</td>
            <td class="text-xs-left">{{ props.item.branch_address }}</td>
            <td class="text-xs-left">{{ props.item.branch_phone_number }}</td>
            <td class="text-xs-left">
              <v-icon small class="mr-2 hover-icon" @click="showFormEdit(props.item)">edit</v-icon>

              <v-icon small class="mr-2 hover-icon" @click="showDialog(props.item.id)">delete</v-icon>
            </td>
          </template>

          <!--No data -->
          <template slot="no-data">
            <v-alert color="error" icon="warning">Sorry, nothing to display here :(</v-alert>
          </template>

          <!--Search no result -->
          <v-alert
            slot="no-results"
            :value="true"
            color="error"
            icon="warning"
          >Your search for "{{ search }}" found no results.</v-alert>
        </v-data-table>
        <!-- End Table Component -->
      </app-card>
    <branch-edit/>
    <branch-add/>
    <v-dialog v-model="dialog" persistent max-width="450">
      <v-card>
        <v-card-title class="headline font-weight-bold grey lighten-3">
          <v-icon large color="warning" class="mr-2">warning</v-icon>Do you want delete this item ?
        </v-card-title>
        <v-divider class="mt-0"></v-divider>
        <v-card-actions>
            <v-spacer></v-spacer>
          <v-btn color="secondary" outlined small @click="dialog = false">Disagree</v-btn>
          <v-btn color="warning" outlined small @click="deleteItem">Agree</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import { get, post, put, del, getWithData } from "../../../api/index.js";
import config from "../../../config/index.js";
import BranchEdit from "./BranchEdit.vue";
import BranchAdd from "./BranchAdd.vue";

import Vue from "vue";

export default {
  name: "Branches",
  components: {
    BranchEdit,
    BranchAdd
  },

  data() {
    return {
      headers: [
        { text: "ID", value: "id", width: "60%" },
        { text: "Branch Name", value: "name" },
        { text: "Username", value: "user.username" },
        { text: "Password", value: "branch_password" },
        { text: "Address", value: "branch_address" },
        { text: "Phone", value: "branch_phone_number" },
        { text: "Action", sortable: false }
      ],
      items: [],
      search: "",
      pagination: {
        rowsPerPage: 10
      },
      rowsPerPageItems: [
        10,
        25,
        50,
        100,
        { text: "$vuetify.dataIterator.rowsPerPageAll", value: -1 }
      ],
      company: JSON.parse(localStorage.getItem("user")),
      dialog: false,
      itemIdToDelete: ""
    };
  },
  created() {
    this.fetchData();
  },
  mounted() {
    this.$root.$on("reloadTableBranch", res => this.fetchData());
  },
  methods: {
    fetchData() {
      get("company/branches?companyId=" + this.company.company_id).then(
        response => {
          this.items = response.data;
        }
      );
    },
    showFormEdit(item) {
      this.$root.$emit("sendEventBranchEdit", {
        showNavigation: true,
        data: item
      });
    },
    showFromAdd() {
      this.$root.$emit("sendEventBranchAdd", {
        showNavigation: true
      });
    },
    deleteItem() {
      del("branches/" + this.itemIdToDelete)
        .then(res => {
          if (res && res.success) {
            this.$notify({
              title: "Success",
              message: "Delete Item Successfully.",
              type: "success"
            });
            this.fetchData();
            this.dialog = false;
          }
        })
        .catch(e => {
          console.log(e);
        });
    },
    showDialog(item) {
      this.dialog = true;
      this.itemIdToDelete = item;
    },
    toggleAll() {
      if (this.selected.length) this.selected = [];
      else {
        this.selected = this.desserts.slice();
      }
    },
    changeSort(column) {
      var columnsNoSearch = ["actions"];
      if (columnsNoSearch.indexOf(column) > -1) {
        return;
      }
      this.loading = true;
      if (this.pagination.sortBy === column) {
        this.pagination.descending = !this.pagination.descending;
      } else {
        this.pagination.sortBy = column;
        this.pagination.descending = false;
      }
      this.loading = false;
    }
  }
};
</script>

