<script>
//import files
import { get, post, put, del } from "../../../api";
import { mapGetters } from "vuex";
import Vue from "vue";
import OrderDetail from "./OrderDetail.vue";

export default {
  name: "Orders",
  props: [
    'done',
    'paid',
    'pending',
    'cancel',
    'booking',
  ],
  components: {
    // TransactionItem
    "order-detail": OrderDetail
  },
  data() {
    return {
      dialog: false,
      paramsSearchTag: {
        status: ""
      },
      desserts: [],
      headers: [
        {
          text: "ID",
          align: "left",
          value: "id",
          class: "mb-icon"
        },
        {
          text: "Branch",
          align: "left",
          value: "branch.name",
          class: "mb-icon"
        },
        {
          text: "Photographer",
          align: "left",
          value: "photographer.name",
          class: "mb-icon"
        },
        {
          text: "Room",
          align: "left",
          value: "customer.room.room_hash",
          class: "mb-icon"
        },
        {
          text: "Email",
          align: "left",
          value: "customer.user.email",
          class: "mb-icon"
        },
        {
          text: "Amount",
          align: "left",
          value: "total_amount_to_dollar",
          class: "mb-icon"
        },
        {
          text: "Payment Method",
          align: "left",
          value: "payment_method",
          class: "mb-icon"
        },
        {
          text: "Status",
          align: "left",
          value: "actions",
          sortable: false
        },
        {
          text: "Action",
          align: "left",
          sortable: false
        }
      ],
      pagination: {},
      loading: true,
      search: "",
      drawer: false,
      item: null,
      eventType: "",
      params: {
        defaultDay: "default"
      },
      itemIdToDelete: null,
      rowsPerPageItems: [
        10,
        20,
        50,
        100,
        { text: "$vuetify.dataIterator.rowsPerPageAll", value: -1 }
      ],
      company_id: JSON.parse(localStorage.getItem("user")).company_id
    };
  },
  computed: {
    ...mapGetters(["rtlLayout"]),
    loadingCom() {
      return this.loading;
    },
    itemsToView() {
      return this.desserts;
    }
  },
  mounted() {
    this.$root.$on("loadOdersWithTime", res => {
      let params = res.params;
      this.params = params;
      this.loading = true;
      this.fetchData(params);
    });

    this.$root.$on("closeDrawerItem", res => {
      this.drawer = res;
    });

    this.$root.$on("editItemSucess", res => {
      this.loading = true;
      this.fetchData(this.params);
    });

    // this.$root.$on("searchTag", res => {
    //   this.params = res;
    //   console.log(this.params);
    //   this.params.check = 1;
    //   this.fetchData(this.params);
    // });
  },
  methods: {
    searchTag(params) {
      this.params.status = params;
      this.params.check = 1;
      console.log(this.params);
      this.fetchData(this.params);
      // this.$root.$emit("searchTag", this.paramsSearchTag);
    },
    fetchData(params) {
      params.company_id = this.company_id;
      let url = "order/history-order";
      post(url, params)
        .then(res => {
          if (res && res.success) {
            this.desserts = res.data;
            this.loading = false;
            this.$root.$emit("total-companies", this.desserts.length);
          }
        })
        .catch(e => {
          console.log(e);
        });
    },
    transactionEvent(event, item) {
      this.drawer = true;
      this.$root.$emit("disabled-transaction-item");
      this.eventType = event;
      this.item = item;
    },
    showDialog(id) {
      this.dialog = true;
      this.itemIdToDelete = id;
    },
    deleteItem() {
      del("orders/" + this.itemIdToDelete)
        .then(res => {
          if (res && res.success) {
            Vue.notify({
              type: "success",
              title: "Delete Item Successfully",
              position: "top right"
            });
            this.loading = true;
            this.fetchData(this.params);
            this.dialog = false;
          }
        })
        .catch(e => {
          console.log(e);
        });
    },
    showInfo(item) {
      this.$root.$emit("showDetailOrder", { showNavigation: true, data: item });
    }
  },

  created() {
    this.loading = true;
    this.fetchData(this.params);
  }
};
</script>

