<template>
  <v-container fluid pt-0 grid-list-xl mt-4>
    <v-row>
      <app-card
        colClasses="xl12 lg12 md12 sm12 xs12"
        customClasses="p-0 elevation-5"
        :fullScreen="true"
        :reloadable="true"
        :closeable="false"
      >
        <!-- <v-toolbar flat color="white">
          <v-toolbar-title>Ewallet Withdraw</v-toolbar-title>
        </v-toolbar>
        <v-divider class="m-0"></v-divider> -->

        <!--Search Component -->
        <v-card-title class="py-4">
          <span class="headline">Ewallet</span>
          <v-spacer/>
            <input type="search" v-model="search" class="cari" id="search" name="search" placeholder="Search">
          <!-- <v-btn small fab dark color="indigo" @click="showFromAdd()" class="ml-2 btn-gradient-primary">
							<v-icon dark>add</v-icon>
          </v-btn>-->
        </v-card-title>
        <!--Search Component -->

        <v-data-table
          :headers="headers"
          :items="items"
          class="elevation-5 body-2 global-custom-table"
          :pagination.sync="pagination"
          :rows-per-page-items="rowsPerPageItems"
          default-sort="id:desc"
          :search="search"
        >
          <v-progress-linear slot="progress" color="blue" indeterminate></v-progress-linear>

          <template slot="headers" slot-scope="props">
            <tr>
              <th
                v-for="header in props.headers"
                :key="header.text"
                :class="['column sortable', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
                @click="changeSort(header.value)"
              >
                <div class="custom-header">
                  <v-tooltip bottom>
                    <span
                      slot="activator"
                      class="text-capitalize font-weight-bold"
                    >{{ header.text }}</span>
                    <span>{{ header.text }}</span>
                  </v-tooltip>
                  <v-icon v-if="header.text != 'Action' && header.text != 'No.'">arrow_upward</v-icon>
                </div>
              </th>
            </tr>
          </template>

          <template v-slot:item="{ item }">
            <tr>
            <td>{{index}}</td>
            <td class="text-xs-left">{{item.company.name}}</td>
            <td class="text-xs-left">{{item.account_holder_name}}</td>
              <td class="text-xs-left">{{item.bank_name}}</td>
              <td class="text-xs-left">{{item.bank_account_number}}</td>
            <td class="text-xs-left">{{item.amount}}</td>
            <td class="text-xs-left custom-v-select d-flex align-items-center">
              <!-- <v-btn color="btn-gradient-primary" small v-if="props.item.status === 'DONE'">{{props.item.status}}</v-btn>
              <v-btn color="btn-gradient-pink" small v-else>{{props.item.status}}</v-btn>-->
                {{item.status}}
             <!-- <v-select
                :items="selectStatus"
                v-model="props.item.status"
                outlined
                class="height-input-customer width-input"
                :readonly="props.item.status == 'DONE' ? true : false"
                @change="updateStatusEwallet(props.item)"></v-select>-->
            </td>
            <td class="text-xs-left action-width-photographer">
              <v-icon small class="mr-2 hover-icon" @click="showInfo(item)">visibility</v-icon>
              <v-icon small class="mr-2 hover-icon" @click="editInfo(item)">edit</v-icon>
              <v-icon small class="hover-icon" @click="deleteDialog">delete</v-icon>
            </td>
            </tr>
          </template>

          <!--No data -->
          <template slot="no-data">
            <v-alert color="error" icon="warning">Sorry, nothing to display here :(</v-alert>
          </template>

          <!--Search no result -->
          <v-alert
            slot="no-results"
            :value="true"
            color="error"
            icon="warning"
          >Your search for "{{ search }}" found no results.</v-alert>
        </v-data-table>

        <ewallet-withdraw/>
        <ewallet-edit/>
      </app-card>
    </v-row>

    <!-- confirm delete -->
      <delete-confirmation-dialog ref="deleteConfirmationDialog" @onConfirm="deleteItem">
      </delete-confirmation-dialog>
    <!-- confirm delete -->
  </v-container>
</template>

<script>
import { get, post, put, del, getWithData } from "../../../api/index.js";
import EwalletInformation from "./EwalletInformation.vue";
import EwalletEdit from "./EwalletEdit";
import Vue from "vue";

export default {
  name: "EwalletWithdraw",
  components: {
    "ewallet-withdraw": EwalletInformation,
    "ewallet-edit": EwalletEdit
  },

  data() {
    return {
      headers: [
        { text: "No.", sortable: false },
        { text: "Company name", value: "company.name", sortable: false },
        {
          text: "Acc. Holder Name",
          value: "bank_account_number",
          sortable: false
        },
        { text: "Bank name", value: "bank_name", sortable: false },
        {
          text: "Bank account number",
          value: "bank_account_number",
          sortable: false
        },
        { text: "Amount", value: "amount", sortable: false },
        { text: "Status", value: "status", sortable: false },
        { text: "Action", sortable: false }
      ],
      items: [],
      pagination: {
        rowsPerPage: 25
      },
      rowsPerPageItems: [
        10,
        25,
        50,
        100,
        { text: "$vuetify.dataIterator.rowsPerPageAll", value: -1 }
      ],
      loading: false,
      dialog: false,
      search: "",
      selectStatus: ["DONE", "PENDING","REJECT"],
      valueSelect: ""
    };
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      get("ewallet-withdraw")
        .then(res => {
          if (res && res.success) {
            this.items = res.data;
          }
        })
        .catch(error => {
          console.log(error.response);
        });
    },
    changeSort(column) {
      var columnsNoSearch = ["actions"];
      if (columnsNoSearch.indexOf(column) > -1) {
        return;
      }
      this.loading = true;
      if (this.pagination.sortBy === column) {
        this.pagination.descending = !this.pagination.descending;
      } else {
        this.pagination.sortBy = column;
        this.pagination.descending = false;
      }
      this.loading = false;
    },
    updateStatusEwallet(item) {
      put(
        "ewallet-withdraw/update-status/" + item.id,
        item
      ).then(res => {
        if (res && res.success) {
          Vue.notify({
            group: "loggedIn",
            type: "success",
            text: "Update status success"
          });
        }
      });
    },
    showInfo(item) {
      this.$root.$emit("showInfoEwallet", { showNavigation: true, data: item });
    },
    editInfo(item) {
          this.$root.$emit("editInfoEwallet", { showNavigation: true, data: item });
    }
  }
};
</script>

<style lang="scss" scoped>
    .v-input__slot {
        margin-bottom: 0px;
    }
</style>
