<script>
    import {mapGetters} from "vuex";
    import {post} from "../../api";
    import PhotoCell from "./components/PhotoCell";
    export default {
        name: "ShopPhotos",
        components: {
            PhotoCell
        },
        data() {
            return {
                showOther: true,
                showSelected: true,
                shopImages: [],
                shopImagesOriginal: [],
                zoomClass: '',
                scrollable: false,
                dialog: false,
                slideAction: false,
                slideShowInterval: null,
                index: 1,
                slidePhotos: [],
                photoSelected: [],
                photoSelectedProperty: [],
                currencies: ["USD", "EURO"],
                languages: ["EN", "RU", "DE", "TR", "AR", "FR", "ID"],
                selectCurrency: "USD",
                selectLanguage: "EN",
                Order: {
                    dialog: false,
                    menu1: false,
                    name: '',
                    roomNumber: '',
                    email: '',
                    countryCode: '',
                    countryCodes: ['(+00)Russia', '(+00)Germany', '(+00)Australia', '(+00)Turkey', '(+00)Indonesia', '...'],
                    checkOutDate: new Date().toISOString().substr(0, 10),
                },
            };
        },

        mounted() {
            document.addEventListener('contextmenu', function (e) {
                e.preventDefault();
            });
            document.onkeydown = function (e) {
                if (e.keyCode === 123) {
                    return false;
                }
                if (e.ctrlKey && e.shiftKey && e.keyCode == 'I'.charCodeAt(0)) {
                    return false;
                }
                if (e.ctrlKey && e.shiftKey && e.keyCode == 'C'.charCodeAt(0)) {
                    return false;
                }
                if (e.ctrlKey && e.shiftKey && e.keyCode == 'J'.charCodeAt(0)) {
                    return false;
                }
                if (e.ctrlKey && e.keyCode == 'U'.charCodeAt(0)) {
                    return false;
                }
            };
            this.$nextTick(function () {
                this.initToTopButton();
            });
            if (this.$vuetify.breakpoint.smAndDown) {
                this.zoomClass = 'xs6 sm4 md3 z8';
            }
            else {
                this.zoomClass = 'xs12 sm6 md4 z6';
            }
        },
        computed: {
            ...mapGetters({
            }),
            checkOutDateFormatted() {
                return this.formatDate(this.Order.checkOutDate);
            },
            photoPackages() {
                return ['10x15', '15x21', '20x30', 'Digital', 'Album']
            },
            slidePhotosComputed() {
                return this.slidePhotos;
            },
            countSelected() {
                return this.photoSelected.length;
            },
            countOtherPhoto() {
                return this.shopImages.length;
            },
            photoNameComputed() {
                return item => item.replace(/\S+\/(\S+[.jpg|.JPG])/, '$1');
            }
        },
        created() {
            let roomId = this.$route.params.roomId;
            let branchId = this.$store.getters.getUser.branch_id

            post('getPhotosWithRoomHash', {
                room_hash: roomId,
                branch_id: branchId

            }).then((res) => {
                if(res && res.success) {
                  console.log(res.data)

                    this.shopImagesOriginal = res.data.images.slice();
                    this.shopImages = res.data.images.slice();
                }
            })
        },
        methods: {
            formatDate(date) {
                if (!date) return null;
                const [year, month, day] = date.split('-');
                return `${month}/${day}/${year}`;
            },
            zoom_out() {
                let zoomClass = this.zoomClass.split(' ').pop();
                switch (zoomClass) {
                    case 'z4':
                        this.zoomClass = 'xs12 sm12 md12 z5';
                        break;
                    case 'z5':
                        this.zoomClass = 'xs12 sm12 md6 z6';
                        break;
                    case 'z6':
                        this.zoomClass = 'xs12 sm6 md4 z7';
                        break;
                    case 'z7':
                        this.zoomClass = 'xs6 sm4 md3 z8';
                        break;
                    default:
                }
            },
            zoom_in() {
                let zoomClass = this.zoomClass.split(' ').pop();
                    switch (zoomClass) {
                        case 'z8':
                            this.zoomClass = 'xs12 sm6 md4 z7';
                            break;
                        case 'z7':
                            this.zoomClass = 'xs12 sm12 md6 z6';
                            break;
                        case 'z6':
                            this.zoomClass = 'xs12 sm12 md12 z5';
                            break;
                        case 'z5':
                            this.zoomClass = 'xs12 sm12 md12 z4';
                            break;
                        default:
                    }
            },
            plusQuantity(index, i) {
                this.photoSelectedProperty[index]['quantity'][i]++;
                if(this.photoSelectedProperty[index]['quantity'][i] === 1) {
                    this.photoSelectedProperty[index]['selected'].push(i);
                }
                this.$forceUpdate();
            },
            minusQuantity(index, i) {
                if( this.photoSelectedProperty[index]['quantity'][i] > 0) {
                    this.photoSelectedProperty[index]['quantity'][i]--;
                    if(this.photoSelectedProperty[index]['quantity'][i] === 0) {
                        this.photoSelectedProperty[index]['selected'].splice(this.photoSelectedProperty[index]['selected'].indexOf(i),1);
                    }
                    this.$forceUpdate();
                }
            },
            initToTopButton() {
                $(document).bind('scroll', function () {
                    if ($(document).scrollTop() > 250) {
                        this.scrollable = true;
                    } else {
                        this.scrollable = false;
                    }
                }.bind(this));
            },
            scrollToTop() {
                $('html,body').stop().animate({
                    scrollTop: 0
                }, 'slow', 'swing');
            },
            closeSlide() {
                this.dialog = false;
                this.slideAction = false;
                this.index = 1;
                // this.slidePhotos = [];
                clearInterval(this.slideShowInterval);


            },
            async slideShows() {
                this.slideAction = !this.slideAction;

                if (this.slideAction) {
                    this.slideShowInterval = await setInterval(() => {
                        this.slidePhotos = [];
                        this.index = this.index + 1;
                        this.showSlideWithImageId(this.index);
                        if (this.index === this.slidePhotos.length - 1) {
                            this.index = -1;
                        }
                    }, 2000);
                } else {
                    clearInterval(this.slideShowInterval);
                }
            },

            removeItem(photo) {
                var i = this.photoSelected.indexOf(photo);
                if (i > -1) {
                    this.photoSelected.splice(i, 1);
                    this.shopImages.splice(this.photoSelectedProperty[i].index, 0, photo);
                    this.photoSelectedProperty.splice(i,1);
                }
            },
            removeAllItem() {
                this.shopImages = this.shopImagesOriginal.slice();
                this.photoSelected = [];
                this.photoSelectedProperty = [];
            },
            selectAllPhotos() {
                this.photoSelected = this.shopImagesOriginal.slice();
                this.shopImagesOriginal.forEach((value, index) => {
                    this.photoSelectedProperty.push({index: index, quantity: [0,0,0,0,0], selected: []});
                });
                this.shopImages = [];
            },
            selectPhoto(photo, index) {
                if (!this.photoSelected.includes(photo)) {
                    this.shopImages.splice(index, 1);
                    this.photoSelected.push(photo);
                    this.photoSelectedProperty.push({
                        index: this.shopImagesOriginal.indexOf(photo),
                        quantity: [0, 0, 0, 0, 0],
                        selected: []
                    });
                }
            },
            // slide handle click next or previus
            async click(e) {
                if (
                    e.target.className === "v-icon fas fa-chevron-circle-right theme--dark"
                ) {
                    this.index = this.index + 1;
                    this.slidePhotos = [];
                    await this.showSlideWithImageId(this.index);
                }

                if (
                    e.target.className === "v-icon fas fa-chevron-circle-left theme--dark"
                ) {
                    this.index = this.index - 1;
                    this.slidePhotos = [];
                    await this.showSlideWithImageId(this.index);
                }
            },
            async showSelectedSildeWithImageId(index) {
                this.dialog = true;
                this.index = index;
                this.slidePhotos = [];
                await this.createSlideWithIndex(this.photoSelected, index);
                // show first image
                let wItem = document.getElementsByClassName(`v-window-item`);

                wItem[0].style.display = "block";
                // get image contain

                let i = 0;
                let carouselItem = document.getElementById(`carousel-item-${i}`);
                let imgItems = carouselItem.getElementsByClassName(
                    "v-image__image v-image__image--contain"
                );
                // custom slide
                this.calculatePos(imgItems[0], i);
                document.getElementsByClassName("v-carousel__next")[0].getElementsByTagName("button")[0].style.display = 'none';
                document.getElementsByClassName("v-carousel__prev")[0].getElementsByTagName("button")[0].style.display = 'none';
                document.getElementsByClassName("fa-play-circle")[0].style.display = 'none';
            },
            async showSlideWithImageId(index) {
                console.log(index);
/*                this.dialog = true;
                this.index = index;
                this.slidePhotos = [];
                await this.createSlideWithIndex(this.shopImages, index);
                // show first image
                let wItem = document.getElementsByClassName(`v-window-item`);

                wItem[0].style.display = "block";
                // get image contain

                let i = 0;
                let carouselItem = document.getElementById(`carousel-item-${i}`);
                let imgItems = carouselItem.getElementsByClassName(
                    "v-image__image v-image__image--contain"
                );
                // custom slide
                this.calculatePos(imgItems[0], i);*/
            },
            createSlideWithIndex(arr, index) {
                let photos = arr;
                let tmp1 = [],
                    tmp2 = [];
                photos.forEach((p, key) => {
                    if (key >= index) {
                        tmp1.push(p);
                    } else {
                        tmp2.push(p);
                    }
                });
                // concat array
                this.slidePhotos = tmp1.concat(tmp2);
                // get next and prev btn
                let nextDiv = document.getElementsByClassName("v-carousel__next")[0];
                console.log('nextDiv', nextDiv);
                let nextBtn = nextDiv.getElementsByTagName("button")[0];
                let prevDiv = document.getElementsByClassName("v-carousel__prev")[0];
                let prevBtn = prevDiv.getElementsByTagName("button")[0];
                // first image in arr
                if (index === 0) {
                    prevBtn.style.display = "none";
                } else {
                    prevBtn.style.display = "block";
                }
                // image arr length - 1
                if (index === this.slidePhotos.length - 1) {
                    nextBtn.style.display = "none";
                } else {
                    nextBtn.style.display = "block";
                }
            },
            calculatePos(mainImg, index) {
                // var url = $(mainImg)
                //     .css("background-image")
                //     .replace("url(", "")
                //     .replace(")", "")
                //     .replace("'", "")
                //     .replace('"', "");
                // url = url.split('"')[0];

                var bgImg = $("<img />");
                bgImg.hide();
                bgImg.bind("load", function () {
                    var height = $(this).height();
                    var width = $(this).width();

                    let padding = 0;
                    if ($(window).width() >= 768) {
                        padding = 70 * 2;
                    } else {
                        padding = 30 * 2;
                    }

                    let widthResize = (width * ($(window).height() - padding)) / height;
                    let widthFromScreenEdgeToImg = ($(window).width() - widthResize) / 2;
                    let left = widthFromScreenEdgeToImg + widthResize - 200 - 20;
                    let top = ($(window).height() - 150 * 3 - 8 * 3 - padding) / 2;

                    console.log('window width', $(window).width(), 'window height', $(window).height());
                    $(`#pt-img-key-${index}`)
                        .css("top", top)
                        .css("left", left);

                });
                $(mainImg).append(bgImg);
                // bgImg.attr("src", url).attr("slide-img" + index);
            },
        }
    };
</script>

