<template>
  <v-container fluid pt-0 grid-list-xl mt-4>
    <v-row>
      <app-card
        colClasses="xl12 lg12 md12 sm12 xs12"
        customClasses="p-0 elevation-5"
        :fullScreen="true"
        :reloadable="true"
        :closeable="false"
      >
        <!--Search Component -->
        <v-card-title class="py-2">
          <span class="headline">Currencies</span>
          <v-spacer/>
            <div style="width: 150px">
                <v-select
                    class="m-0"
                    :items="['Manual']"
                    value="Manual"
                    readonly
                ></v-select>
            </div>
        </v-card-title>
        <!--Search Component -->
<!--          <table>
              <thead>
                  <th>USD(Default)</th>
                  <th>EURO</th>
                  <th>TRY</th>
                  <th>IDR</th>
              </thead>
              <tbody>
                <tr>
                    <td>1</td>
                    <td>{{currencyObject[2]}}</td>
                    <td>{{currencyObject[3]}}</td>
                    <td>{{currencyObject[4]}}</td>
                </tr>
              </tbody>
          </table>-->
        <v-data-table
          :headers="headers"
          :items="items"
          class="elevation-5 body-2 global-custom-table"
          hide-actions
        >
          <v-progress-linear slot="progress" color="blue" indeterminate></v-progress-linear>

          <template slot="headers" slot-scope="props">
            <tr>
              <th
                v-for="header in props.headers"
                :key="header.text"
                :class="['column sortable']"
              >
                <div class="custom-header">
                  <v-tooltip bottom>
                    <span
                      slot="activator"
                      class="text-capitalize font-weight-bold"
                    >{{ header.text }}</span>
                    <span>{{ header.text }}</span>
                  </v-tooltip>
                </div>
              </th>
            </tr>
          </template>

          <template v-slot:item="{ item }">
              <tr>
                  <td class="text-xs-left">1</td>
                  <td class="text-xs-left">
                      <v-menu
                          v-model="menu1"
                          :close-on-content-click="false"
                          :nudge-width="100"
                          offset-y
                      >
                        <span slot="activator">
                            {{item[2]}}
                        </span>
                          <v-card>
                              <v-card-title class="pb-0">
                                  Update Euro
                              </v-card-title>
                              <v-card-text class="py-0 my-0">
                                  <v-text-field
                                      placeholder="Euro"
                                      v-model="Euro"
                                  ></v-text-field>
                              </v-card-text>
                              <v-card-actions class="pt-0">
                                  <v-btn text @click="menu1 = false">Cancel</v-btn>
                                  <v-spacer/>
                                  <v-btn color="primary" flat @click="editItem('Euro', Euro)">Save</v-btn>
                              </v-card-actions>
                          </v-card>
                      </v-menu>
                  </td>
                  <td class="text-xs-left">
                      <v-menu
                          v-model="menu2"
                          :close-on-content-click="false"
                          :nudge-width="100"
                          offset-y
                      >
                    <span slot="activator">
                        {{item[3]}}
                    </span>
                          <v-card>
                              <v-card-title class="pb-0">
                                  Update Idr
                              </v-card-title>
                              <v-card-text class="py-0 my-0">
                                  <v-text-field
                                      placeholder="Idr"
                                      v-model="Idr"
                                  ></v-text-field>
                              </v-card-text>
                              <v-card-actions class="pt-0">
                                  <v-btn text @click="menu2 = false">Cancel</v-btn>
                                  <v-spacer/>
                                  <v-btn color="primary" flat @click="editItem('Idr', Idr)">Save</v-btn>
                              </v-card-actions>
                          </v-card>
                      </v-menu>
                  </td>
                  <td class="text-xs-left">
                      <v-menu
                          v-model="menu3"
                          :close-on-content-click="false"
                          :nudge-width="100"
                          offset-y
                      >
                    <span slot="activator">
                        {{item[4]}}
                    </span>
                          <v-card>
                              <v-card-title class="pb-0">
                                  Update Try
                              </v-card-title>
                              <v-card-text class="py-0 my-0">
                                  <v-text-field
                                      placeholder="Try"
                                      v-model="Try"
                                  ></v-text-field>
                              </v-card-text>
                              <v-card-actions class="pt-0">
                                  <v-btn text @click="menu3 = false">Cancel</v-btn>
                                  <v-spacer/>
                                  <v-btn color="primary" flat @click="editItem('Try', Try)">Save</v-btn>
                              </v-card-actions>
                          </v-card>
                      </v-menu>
                  </td>
              </tr>
          </template>

        </v-data-table>
      </app-card>
    </v-row>
  </v-container>
</template>

<script>
import { get, post, put, del, getWithData } from "../../../api/index.js";
import Vue from "vue";

export default {
  name: "Currencies",
  data() {
    return {
      headers: [
        { text: "USD(Default)", sortable: false },
        { text: "EURO", value: "company.name", sortable: false },
        { text: "IDR", value: "company.name", sortable: false },
        { text: "TRY", value: "company.name", sortable: false },

      ],
      items: [],
      currencyObject: {},
      loading: false,
      dialog: false,
      menu1: false,
      menu2: false,
      menu3: false,
      Euro: 0,
      Idr: 0,
      Try: 0
    }
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      get("exchange_rates")
        .then(res => {
          if (res && res.success) {
            this.items = [];
            this.currencyObject = {};
            res.data.forEach((item) => {
                this.currencyObject[item.from_currency_id] = 1 / item.rate;
            });
            this.items.push(this.currencyObject);
            this.Euro = this.currencyObject[2];
            this.Idr = this.currencyObject[3];
            this.Try = this.currencyObject[4];
          }
        })
        .catch(error => {
          console.log(error.response);
        });
    },
    editItem(currencyName, value) {
        let id = 1;
        switch (currencyName) {
            case 'Euro':
                id = 1;
                break;
            case 'Idr':
                id = 2;
                break;
            case 'Try':
                id = 3;
                break;
        }
        this.updateExchangeRate(id, 1 / value);
    },
    updateExchangeRate(id, rate) {
      put(
        "exchange_rates/" + id,
        {
            'from_currency_id': id + 1,
            'to_currency_id' : 1,
            'rate': rate
        }
      ).then(res => {
        if (res && res.success) {
          this.menu1 = this.menu2 = this.menu3 =false;
          setTimeout(() => {
                Vue.notify({
                    group: "loggedIn",
                    type: "success",
                    text: "Update success!"
                });
            }, 1500);
          window.location.reload();
        }
      });
    }
  }
};
</script>

