<template>
    <v-container fluid>
        <app-card
            v-if="!orderShow"
        >
            <v-card-title flat color="white" class="py-2">
                <span :class="$vuetify.breakpoint.xsOnly? 'font-md': 'font-lg'">
                    <span>Customers</span><span>(2345)</span>
                </span>
                <v-spacer/>
<!--                <div class="w-25">-->
                    <input type="search" v-model="search" class="cari" id="search" name="search"
                           placeholder="Enter Search Value">
<!--                </div>-->
                <v-btn small fab color="#ededed" icon class="btn-add">
                    <v-icon color="grey">add</v-icon>
                </v-btn>
                <v-btn :href="urlExport" small fab color="#ededed" icon class="btn-add m-0">
                    <v-icon color="grey">cloud_download</v-icon>
                </v-btn>
            </v-card-title>
            <v-data-table
                :headers="headers"
                :items="items"
                class="elevation-5"
                :pagination.sync="pagination"
                :rows-per-page-items="rowsPerPageItems"
                default-sort="id:desc"
                :search="search">
                <template slot="headers" slot-scope="props">
                    <tr>
                        <th
                            v-for="header in props.headers"
                            :key="header.text"
                            :class="['column sortable th-margin', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
                            @click="changeSort(header.value)">
                            <v-tooltip bottom>
                                <span slot="activator" class="text-capitalize font-weight-bold">
                                    {{ header.text }}
                                </span>
                                <span>
                                    {{ header.text }}
                                </span>
                            </v-tooltip>
                            <v-icon
                                v-if="header.text != 'Action' && header.text != 'Order' && header.text != 'Invoice' && header.text != 'Rest Photo' && header.text != 'Avatar'">
                                arrow_upward
                            </v-icon>
                        </th>
                    </tr>
                </template>

                <template slot="items" slot-scope="props">
                    <td>{{ props.item.id }}</td>
                    <td>----</td>
                    <td>{{ props.item.name }}</td>
                    <td>{{ props.item.room.room_hash }}</td>
                    <td>{{ props.item.user.email }}</td>
                    <td>
                        <v-btn rounded small class="btn-customer" @click="orderShow = !orderShow">Manage</v-btn>
                    </td>
                    <td>
                        <v-btn rounded small class="btn-customer">100</v-btn>
                    </td>
                    <td>
                        <v-text-field
                            name="name"
                            label="Offer"
                            class="height-input-customer width-input"></v-text-field>
                    </td>
                    <td>{{ props.item.status ? 'PAID' : '' }}</td>
                    <td>
                        <v-icon
                            small
                            class="mr-6 hover-icon margin-icon-customer"
                            @click="showFormDetail(props.item)"
                        >
                            remove_red_eye
                        </v-icon>
                        <v-icon
                            small
                            class="mr-6 hover-icon margin-icon-customer"
                            @click="showFormEdit(props.item)"
                        >
                            edit
                        </v-icon>

                        <v-icon
                            small
                            class="mr-6 hover-icon margin-icon-customer"
                            @click="showDialog(props.item.id)"
                        >
                            delete
                        </v-icon>
                        <br/>
                        <v-icon
                            small
                            class="mr-6 hover-icon margin-icon-customer"
                        >
                            videocam
                        </v-icon>
                        <v-icon
                            small
                            class="mr-6 hover-icon margin-icon-customer"
                        >
                            star
                        </v-icon>
                    </td>
                </template>

                <template slot="no-data">
                    <v-alert  color="error" icon="warning">
                        Sorry, nothing to display here :(
                    </v-alert>
                </template>

                <v-alert slot="no-results" :value="true" color="error" icon="warning">
                    Your search for "{{ search }}" found no results.
                </v-alert>
            </v-data-table>
            <customer-detail/>
            <customer-edit/>


        </app-card>
        <orders v-else/>
        <v-dialog v-model="dialog" persistent max-width="450">
            <v-card>
                <v-card-title class="headline font-weight-bold grey lighten-3">
                    <v-icon large color="warning" class="mr-2">
                        warning
                    </v-icon>
                    Do you want delete this item ?
                </v-card-title>
                <v-divider class="mt-0"></v-divider>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="secondary" outlined small @click="dialog = false">Disagree</v-btn>
                    <v-btn color="warning" outlined small @click="deleteItem">Agree</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-container>
</template>

<script>
    import {get, post, put, del, getWithData} from '../../../api/index.js';
    import CustomerEdit from './CustomerEdit';
    import CustomerDetail from "./CustomerDetail";
    import Orders from "../order/Orders";

    export default {

        name: 'Customers',
        components: {
            CustomerEdit, CustomerDetail, Orders
        },
        data() {
            return {
                dialog: false,
                orderShow: false,
                search: '',
                items: [],
                headers: [
                    {text: 'ID', value: 'id', width: '1%'},
                    {text: 'Branch', value: 'branch', align: 'left'},
                    {text: 'Name', value: 'name', align: 'left'},
                    {text: 'Room', value: 'room.room_hash', align: 'left'},
                    {text: 'Email', value: 'user.email', align: 'left'},
                    {text: 'Order', align: 'left'},
                    {text: 'Rest Photo', align: 'left'},
                    {text: 'Set Offer(Rp)', width: '10%', align: 'left'},
                    {text: 'Status', width: '10%', align: 'left'},
                    {text: 'Action', sortable: false, width: '10%', align: 'left', value: 'actions'},
                ],
                pagination: {
                    rowsPerPage: 25
                },
                rowsPerPageItems: [25, 50, 100, {"text": "$vuetify.dataIterator.rowsPerPageAll", "value": -1}],
                company: JSON.parse(localStorage.getItem('user')),
                urlExport: 'company/branches/customers/export?companyId=' + JSON.parse(localStorage.getItem('user')).company_id,
                itemIdToDelete: '',
                loading: false
            }
        },
        created() {
            this.fetchData()
        },
        mounted() {
            this.$root.$on('reloadTableCustomer', res => this.fetchData());
            this.$root.$on('back', () => this.orderShow = !this.orderShow);
        },
        methods: {
            fetchData() {
                get('company/branches/customers?companyId=' + this.company.company_id)
                    .then(response => {
                        if (response && response.success) {
                            this.items = response.data
                        }
                    })
                    .catch(error => {
                        console.log(error)
                    })
            },
            deleteItem() {
                var userId = this.company.id
                del('company/branches/customer/delete/' + this.itemIdToDelete + '?userId=' + userId)
                    .then(response => {
                        if (response && response.success) {
                            this.dialog = false
                            this.fetchData()
                            this.$notify({
                                title: 'Success',
                                message: 'Delete Item Successfully.',
                                type: 'success',
                                duration: 2000,
                            })
                        }
                    })
            },
            showDialog(item) {
                this.dialog = true
                this.itemIdToDelete = item
            },
            showFormEdit(item) {
                this.$root.$emit('showFormEditCustomer', {showNavigation: true, data: item})
            },
            showFormDetail(item) {
                console.log('here');
                this.$root.$emit('showFormCustomerDetail', {showNavigation: true, data: item})
            },
            changeSort(column) {
                var columnsNoSearch = ['actions']
                if (columnsNoSearch.indexOf(column) > -1) {
                    return
                }
                this.loading = true
                if (this.pagination.sortBy === column) {
                    this.pagination.descending = !this.pagination.descending
                } else {
                    this.pagination.sortBy = column
                    this.pagination.descending = false
                }
                this.loading = false
            },
            changeTabItems() {

            }
        }
    };
</script>

