<template>
    <div>
        <v-row>
            <v-col cols="0" sm="6" lg="8" class="pa-12 d-none d-sm-flex align-items-center justify-center">
                <v-img src="/images/customer/loginimage.jpg" max-width="100%" max-height="100%"></v-img>
            </v-col>
            <v-col cols="12" sm="6" lg="4" class="d-flex flex-column justify-space-around">
                <v-card flat class="mx-auto text-center">
                    <v-card-title class="font-weight-bold black--text display-1 justify-center"><span>WELCOME</span>
                    </v-card-title>
                    <v-img
                        src="appLogo"
                        class="d-inline-flex"
                        width="100px"></v-img>
                    <v-card-text v-if="checkFormLogin">Login to Download and Purchase Photos</v-card-text>
                    <v-card-text v-if="checkFormPassword">Reset Password to Start Selling Photos</v-card-text>
                    <v-form ref="form" v-model="valid" lazy-validation v-if="checkFormLogin">
                        <v-text-field
                            v-model="email"
                            :rules="emailRules"
                            label="Email"
                            required
                            @keyup.enter="login"></v-text-field>
                        <v-text-field
                            :type="'password'"
                            v-model="password"
                            class="mb-4"
                            :rules="passwordRules"
                            label="Password"
                            required
                            @keyup.enter="login"></v-text-field>
                        <v-btn color="primary" @click="login" block rounded>Login</v-btn>
                    </v-form>

                    <v-form ref="form" v-model="valid" lazy-validation v-if="checkFormPassword">
                        <v-text-field
                            v-model="email"
                            :rules="emailRules"
                            label="Email"
                            required
                            @keyup.enter="login"></v-text-field>
                        <v-btn color="primary" @click="sendEmail()" block rounded>Send Email</v-btn>
                        <v-btn text @click="checkFormPassword = false,checkFormLogin = true" class="mt-4">
                            <v-icon>arrow_back</v-icon>Back to Login
                        </v-btn>
                    </v-form>
                    <v-row class="text-muted justify-space-between align-items-center body-2 mx-0 mt-4" v-if="checkFormLogin">
                      <span class="align-items-center d-inline-flex">
                          <v-icon color="primary">check_circle</v-icon>&nbsp;Remember me
                      </span>
                        <span @click="resetPassword()">Forgot password?</span>
                    </v-row>
                </v-card>
                <div class="mt-12">
                    <v-row class="body-2 justify-center m-0 font-weight-bold">
                        <span @click="dialog = true" class="px-1">Term & Conditions</span>
                        <span @click="dialog = true" class="px-1">Privacy Policy</span>
                        <span @click="dialog = true" class="px-1">Return Policy</span>
                    </v-row>
                    <v-row justify="center body-2 mt-6">
                        <span>All Rights Reserved @ Holipic 2019</span>
                    </v-row>
                </div>
            </v-col>
        </v-row>
        <v-dialog
            v-model="dialog"
            width="500"
        >
            <v-card>
                <v-card-title
                    class="headline grey lighten-2"
                    primary-title
                >
                    Privacy Policy
                </v-card-title>

                <v-card-text>
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore
                    et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut
                    aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse
                    cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in
                    culpa qui officia deserunt mollit anim id est laborum.
                </v-card-text>

            </v-card>
        </v-dialog>
    </div>
</template>

<script>
    import {get} from "../../api/index.js";
    import config from "../../config/index.js";
    import AppConfig from "../../constants/AppConfig";

    export default {
        name: "Login",

        data() {
            return {
                appLogo: AppConfig.appLogo,
                valid: true,
                email: "",
                password: "",
                passwordRules: [
                    v => !!v || "Password is required",
                    v => (v && v.length <= 10) || "Password must be less than 10 characters"
                ],
                emailRules: [
                    v => !!v || "E-mail is required",
                    v => /.+@.+/.test(v) || "E-mail must be valid"
                ],
                checkFormLogin: true,
                checkFormPassword: false,
                dialog: false
            };
        },
        mounted() {
            if (this.$route.params.notificationPassword) {
                this.$notify({
                    title: "Notification",
                    message: "Change Password Success",
                    type: "success",
                    duration: 2000
                });
            }
        },
        created() {
        },
        methods: {
            validate() {
                if (this.$refs.form.validate()) {
                    this.snackbar = true;
                }
            },
            login() {
                if (this.$refs.form.validate()) {
                    const user = {
                        email: this.email,
                        password: this.password
                    };

                    this.$store.dispatch("signinUser", {user});
                }
            },
            resetPassword() {
                this.checkFormLogin = false;
                this.checkFormPassword = true;
            },
            sendEmail() {
                if (this.$refs.form.validate()) {
                    get(config.BASE_URL + "/auth/forgot-password?email=" + this.email).then(
                        res => {
                            if (res.data.success) {
                                this.$notify({
                                    title: "Success",
                                    message: res.data.message,
                                    type: "success",
                                    duration: 2000
                                });
                            } else {
                                this.$notify({
                                    title: "Error",
                                    message: res.data.message,
                                    type: "error",
                                    duration: 2000
                                });
                            }
                        }
                    );
                }
            }
        }
    };
</script>

<style lang="scss" scoped>
</style>
