<template>
  <v-container fluid>
      <app-card>
        <v-toolbar flat color="white">
          <v-toolbar-title>Packages List</v-toolbar-title>
        </v-toolbar>
        <v-divider class="m-0"></v-divider>
          <v-toolbar flat>
              <span>Customer Download Page ONe(1) Digital Photo Price:</span>
              <span class="ml-12 mr-auto">10</span>
              <span ><v-icon>edit</v-icon></span>
          </v-toolbar>
          <v-divider class="m-0"></v-divider>
          <v-divider class="mt-12 mb-0"></v-divider>
          <v-card-title flat color="white" class="py-2">
              <span class="font-lg mr-auto">Single Products(1)</span>
              <v-btn small fab color="#ededed" icon @click="showAddSingleItem()">
                  <v-icon color="grey">add</v-icon>
              </v-btn>
          </v-card-title>
          <single-item></single-item>

          <v-data-table
              :headers="headers2"
              :items="desserts2"
              class="elevation-5"
              :pagination.sync="pagination"
              :disable-initial-sort="true"
              :loading="loadingCom"
          >
              <v-progress-linear slot="progress" color="blue" indeterminate></v-progress-linear>
              <!--Header -->
              <template slot="headers" slot-scope="props">
                  <tr>
                      <th
                          v-for="header in props.headers"
                          :key="header.text"
                          :class="['column sortable', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
                          @click="changeSort(header.value)"
                      >
                          <div
                              :class="header.value == 'price' || header.value == 'offer' ? 'justify-content-center' : ''"
                          >
                              <v-tooltip bottom>
                    <span
                        slot="activator"
                        class="text-capitalize font-weight-bold"
                    >{{ header.text }}</span>
                                  <span>{{ header.text }}</span>
                              </v-tooltip>
                              <v-icon v-if="header.value == 'package_name' || header.value == 'id'">arrow_upward</v-icon>
                          </div>
                      </th>
                  </tr>
              </template>

              <template slot="items" slot-scope="props">
                  <td>{{ props.index + 1 }}</td>
                  <td>{{ props.item.size }}</td>
                  <td>
                      <v-col class="center-prepend-icon">
                          <v-edit-dialog
                              :ref="'v-dialog'+'dollar'+props.item.id"
                              :return-value.sync="props.item.dollar"
                              :large="true"
                              lazy
                              @save="savePhotoPackage('dollar', props.item.dollar, props.item.id)"
                              @cancel="cancel"
                              @open="open()"
                              @close="close"
                          >
                              <div>{{ props.item.dollar }}</div>
                              <div slot="input" class="mt-4 title">Update Dollar</div>
                              <v-text-field
                                  slot="input"
                                  v-model="props.item.dollar"
                                  type="number"
                                  label="Edit"
                                  single-line
                                  min="0"
                                  autofocus
                                  ref="checkPhotoPackage"
                                  :rules="[rules.required, rules.number]"
                              ></v-text-field>
                          </v-edit-dialog>
                      </v-col>
                  </td>
                  <td>
                      ---
                  </td>
                  <td>
                      <v-col class="center-prepend-icon">
                          <v-edit-dialog
                              :ref="'v-dialog'+'free_photo'+props.item.id"
                              :return-value.sync="props.item.free_photo"
                              :large="true"
                              lazy
                              @save="savePhotoPackage('free_photo', props.item.free_photo, props.item.id)"
                              @cancel="cancel"
                              @open="open()"
                              @close="close('free_photo', props.item.id)"
                          >
                              <div>{{ props.item.free_photo }}</div>
                              <div slot="input" class="mt-4 title">Update Free Photo</div>
                              <v-text-field
                                  slot="input"
                                  v-model="props.item.free_photo"
                                  type="number"
                                  label="Edit"
                                  single-line
                                  min="0"
                                  :rules="[rules.required, rules.number]"
                                  ref="checkPhotoPackage"
                              ></v-text-field>
                          </v-edit-dialog>
                      </v-col>
                  </td>
                  <td class="text-right">
                      <v-icon small>edit</v-icon>
                      <v-icon small class="mr-12">delete</v-icon>
                  </td>
              </template>

              <!--No data -->
              <template slot="no-data">
                  <v-alert  color="error" icon="warning">Sorry, nothing to display here :(</v-alert>
              </template>
          </v-data-table>

      </app-card>
      <v-dialog v-model="dialog" persistent max-width="450">
        <v-card>
          <v-card-title class="headline font-weight-bold grey lighten-3">
            <v-icon large color="warning" class="mr-2">warning</v-icon>Do you want delete this item ?
          </v-card-title>
          <v-divider class="mt-0"></v-divider>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="secondary" outlined small @click="dialog = false">Disagree</v-btn>
            <v-btn color="warning" outlined small @click="deleteItem">Agree</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <app-card>
          <v-divider class="mt-12 mb-0"></v-divider>
          <v-card-title flat color="white" class="py-2">
              <span class="font-lg mr-auto">Packages List</span>
              <v-btn small fab color="#ededed" icon @click="showAddItem()">
                  <v-icon color="grey">add</v-icon>
              </v-btn>
          </v-card-title>
          <package-item></package-item>
          <v-data-table
              :headers="headers"
              :items="desserts"
              class="elevation-5"
              :pagination.sync="pagination"
              :loading="loadingCom"
          >
              <v-progress-linear slot="progress" color="blue" indeterminate></v-progress-linear>
              <!--Header -->
              <template slot="headers" slot-scope="props">
                  <tr>
                      <th
                          v-for="header in props.headers"
                          :key="header.text"
                          :class="['column sortable', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '', 'px-3']"
                          @click="changeSort(header.value)"
                      >
                          <v-tooltip bottom>
                              <span slot="activator" class="text-capitalize font-weight-bold">{{ header.text }}</span>
                              <span>{{ header.text }}</span>
                          </v-tooltip>
                          <v-icon v-if="header.value != 'actions'">arrow_upward</v-icon>
                      </th>
                  </tr>
              </template>

              <template slot="items" slot-scope="props">
                  <td>{{ props.index + 1 }}</td>
                  <td>{{ props.item.package_name }}</td>
                  <td>{{ props.item.price }}</td>
                  <td>-----</td>
                  <td>
                     ---
                  </td>
                  <td>
                     ---
                  </td>
                  <td>
                     ---
                  </td>
                  <td>
                     ---
                  </td>
                  <td class="px-1">
                      <v-icon small class="mr-2" @click="showItem(props.item)">visibility</v-icon>
                      <v-icon small class="mr-2" @click="editItem(props.item)">edit</v-icon>
                      <v-icon small class="mr-2" @click="showDialog(props.item.id)">delete</v-icon>
                  </td>
              </template>
              <template slot="no-data">
                  <div class="btn-reset text-center">
                      <v-btn color="primary">Reset</v-btn>
                  </div>
              </template>
          </v-data-table>

      </app-card>
  </v-container>
</template>

<script>
import { get, post, put, del, getWithData } from "../../../api/index.js";
import Vue from "vue";
import PackageItem from "./PackageItem.vue";
import SingleItem from "./SingleItem";
export default {
  name: "Index",

  components: {
    PackageItem, SingleItem
  },

  data() {
    return {
      headers: [
        { text: "Sl No.", value: "id" },
        { text: "Package Name", align: "left", value: "package_name" },
        { text: "Package Price", value: "price" },
        { text: "Total Print", value: "offer" },
        { text: "Digital", value: "offer", sortable: false },
        { text: "Album Type/Name", value: "offer", sortable: false },
        { text: "Gift/Bonus", value: "offer", sortable: false },
        { text: "Total Gift/Bonus", value: "offer", sortable: false },
        { text: "Actions", value: "actions", sortable: false }
      ],
      desserts: [],
      headers2: [
        { text: "Sl No.", value: "id", width: "10%" },
        { text: "Size/Format", value: "package_name", width: "25%" },
        { text: "Price", value: "offer", sortable: false },
        { text: "Web Copy", value: "offer", sortable: false },
        { text: "Free", value: "offer", sortable: false }
      ],
      desserts2: [],
      rules: {
        required: value => !!value || "Required.",
        number: value => {
          const abc = /^[0-9]\d*$/;
          return abc.test(value) || "Please input number.";
        },
        decimal: value => {
          const abc = /^\d+\.\d{0,10}$/;
          return abc.test(value) || "Please input number.";
        }
      },
      dialog: false,
      pagination: {
        page: 1,
        rowsPerPage: 10,
        totalItems: 0
      },
      itemIdToDelete: {
        id: null,
        setting_id: null
      },
      loading: true,
      key: 0,
      keyPhotoPackage: 0,
      user: JSON.parse(localStorage.getItem("user")),
      persistent: true
    };
  },
  computed: {
    loadingCom() {
      return this.loading;
    }
  },
  methods: {
    fetchData() {
      let url = "get-company-package";
      post(url, this.user)
        .then(res => {
          if (res && res.success) {
            this.desserts = res.data;
            this.loading = false;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },

    fetchDataPhotoPackage() {
      let url = "get-photo-package";
      post(url, this.user)
        .then(res => {
          if (res && res.success) {
            this.desserts2 = res.data;
            this.data = res.data;
            this.loading = false;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },

    unDisableItem(key) {
      this.key = key;
    },

    unDisablePhotoPackage(keyPhotoPackage) {
      this.keyPhotoPackage = keyPhotoPackage;
    },

    editPhotoPackage(field_name, value, id) {
      var id = id;
      var field = {
        field_name: field_name,
        value: value,
        id: id
      };

      this.fetchDataEdit(id, field);
    },

    fetchDataEdit(id, field) {
      post("edit/photoPackage/" + id, field)
        .then(res => {
          if (res && res.success) {
            this.$notify({
              title: "Success",
              message: "Edit Item Successfully.",
              type: "success",
              duration: 2000
            });
            this.key = 0;
            this.fetchDataPhotoPackage();
          }
        })
        .catch(e => {
          setTimeout(() => {
            this.alertStt = false;
          }, 1500);
        });
    },

    showAddItem() {
      let obj = {
        check: "add",
        showDialog: true,
        company_id: this.user.company_id
      };
      this.$root.$emit("change-status", obj);
    },
      showAddSingleItem() {
          let obj = {
              check: "add",
              showDialog: true,
              company_id: this.user.company_id
          };
          this.$root.$emit("add-single-item", obj);
      },
    showItem(item) {
      let obj = {
        check: "show",
        showDialog: true
      };
      this.$root.$emit("change-status", obj);
      this.$root.$emit("data-packages", item);
    },

    editItem(item) {
      let obj = {
        check: "edit",
        showDialog: true
      };

      this.$root.$emit("change-status", obj);
      this.$root.$emit("data-packages", item);
    },

    showDialog(id, setting_id) {
      this.dialog = true;
      this.itemIdToDelete.id = id;
    },

    deleteItem(id, setting_id) {
      del("company_packages/" + this.itemIdToDelete.id)
        .then(res => {
          this.loading = true;
          this.fetchData();
          this.dialog = false;
          this.$notify({
            title: "Success",
            message: "Delete Item Successfully.",
            type: "success",
            duration: 2000
          });
        })
        .catch(err => {
          console.log(err);
        });
    },
    toggleAll() {
      if (this.selected.length) this.selected = [];
      else {
        this.selected = this.desserts.slice();
      }
    },
    changeSort(column) {
      var columnsNoSearch = ["actions", "price", "offer"];
      if (columnsNoSearch.indexOf(column) > -1) {
        return;
      }
      this.loading = true;
      if (this.pagination.sortBy === column) {
        this.pagination.descending = !this.pagination.descending;
      } else {
        this.pagination.sortBy = column;
        this.pagination.descending = false;
      }
      this.loading = false;
    },
    updatePackage(id, field) {
      post("edit/companyPackage/" + id, field)
        .then(res => {
          if (res && res.success) {
            this.$notify({
              title: "Success",
              message: "Edit Item Successfully.",
              type: "success",
              duration: 2000
            });

            this.fetchData();
          }
        })
        .catch(e => {
          setTimeout(() => {
            this.alertStt = false;
          }, 1500);
        });
    },
    save(type, value, id) {
      if (!this.$refs.checkPackage.validate()) {
        this.$refs["v-dialog-p" + type + id].isActive = true;
      } else {
        var field = {
          field_name: type,
          value: value,
          id: id
        };
        this.updatePackage(id, field);
      }
    },
    cancel() {
      this.fetchData();

      this.fetchDataPhotoPackage();
    },
    open() {},
    close(type, id) {
      this.fetchData();

      this.fetchDataPhotoPackage();

      this.$refs["v-dialog" + type + id].isActive = false;

      this.$refs["v-dialog-p" + type + id].isActive = false;
    },
    savePhotoPackage(type, value, id) {
      if (!this.$refs.checkPhotoPackage.validate()) {
        this.$refs["v-dialog" + type + id].isActive = true;
      } else {
        var field = {
          field_name: type,
          value: value,
          id: id
        };
        this.fetchDataEdit(id, field);
      }
    }
  },
  mounted() {
    this.$root.$on("reload-table", res => {
      this.loading = true;
      this.fetchData();
    });
  },
  created() {
    this.fetchData();
    this.fetchDataPhotoPackage();
  }
};
</script>

